@import '../../@theme/index';

@keyframes margin-left {
  0% {
    transform: translate(100%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.communityWithRegions {
  position: fixed;
  height: 100%;
  background: #ffffff78;
  width: 75%;
  z-index: 100;
  animation: margin-left 1s ease;
  transition: top 0.3s ease;
  overflow: auto;
  &.menuUsual {
    top: 100px;
  }
  &.menuMiniPaymentFixed {
    top: 150px;
  }
  &.menuMini {
    top: 80px;
  }
  @media (max-width: $sm-extra) {
    width: 100%;
    left: 0;
    background: #fff;
  }

  &__wrap {
    position: absolute;
    right: 0;
    background: $whiteBg;
    width: 90%;
    height: calc(100% - 100px);
    overflow-y: auto;
    overflow-x: hidden;
    @include scrollbars(10px, #4285f4, #f5f5f5);

    -webkit-box-shadow: -12px 0px 20px -7px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: -12px 0px 20px -7px rgba(34, 60, 80, 0.2);
    box-shadow: -12px 0px 20px -7px rgba(34, 60, 80, 0.2);
    &.menuUsual {
      height: calc(90% - 10px);
    }
    &.menuMiniPaymentFixed {
      height: calc(100% - 150px);
    }
    &.menuMini {
      height: calc(90%);
    }
    @media (max-width: $md-extra) {
      width: 100%;
      left: 0;
      right: 0;
      margin: auto;
    }
    @media (max-width: $sm-extra) {
      width: 95%;
      left: 0;
      right: 0;
      margin: auto;
      box-shadow: none;
    }
  }
  &.with-header-fixed-payment {
    max-height: 70%;
    @media (max-width: $md) {
      max-height: 70%;
    }
  }
}
