@import '../../../@theme/index';

.header {
  width: 100%;
  height: 100px;
  z-index: 99;
  background: white;
  margin: auto;
  transition: 0.2s;
  position: relative;
  &.miniHeader {
    height: 80px;
  }
  &.myAccount {
    width: 70%;
    height: 70px;
  }
  &__mainMenu {
    position: fixed;
    z-index: 25;
    top: 0;
    border-bottom: 1px solid #ebebeb;
    width: 100%;
    height: inherit;
    background: white;
    box-sizing: border-box;
    &.myAccount {
      width: 70%;
    }
    &.withShadow {
      -webkit-box-shadow: 0px 4px 16px 1px rgba(0, 0, 0, 0.18);
      -moz-box-shadow: 0px 4px 16px 1px rgba(0, 0, 0, 0.18);
      box-shadow: 0px 4px 16px 1px rgba(0, 0, 0, 0.18);
    }
  }
  &__wrap {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    padding: 0 30px;
    height: 100%;
    box-sizing: border-box;
    &.myAccount {
      width: 100%;
      justify-content: flex-start;
    }
    @media (max-width: $sm-extra) {
      width: 100%;
    }
    @media (max-width: $xl) {
      font-size: 15px;
    }
    @media (max-width: $lg) {
      font-size: 14px;
    }
    @media (max-width: $sm-extra) {
      padding: 0 15px;
    }
  }
  &__logo {
    margin-right: 120px;
    &.myAccount {
      margin-right: 100px;
      order: 1;
      @media (max-width: $xl) {
        margin-right: 50px;
      }
    }
    @media (max-width: $xxl) {
      margin-right: 50px;
    }
    @media (max-width: $xl) {
      order: 2;
    }
  }
  &__signUp {
    order: 2;
    &__link {
      font-family: $roboto;
      font-weight: 300;
      font-size: 16px;
      line-height: 20px;
      text-transform: capitalize;
      text-decoration: none;
      color: $whiteText;
      background: $btnGreenBg;
      padding: 10px 30px;
      border-radius: 30px;
    }
  }
  &__nav {
    height: 100%;
    &.myAccount {
      order: 2;
    }
    @media (max-width: $xl) {
      order: 1;
      min-width: 44px;
      min-height: 44px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
  }
  &__backButton {
    height: 100%;
    order: 3;
    cursor: pointer;
    border-left: 1px solid $borderHeaderPayment;
    border-right: 1px solid $borderHeaderPayment;
    box-sizing: border-box;
    margin: 0 30px;
    @media (max-width: $md) {
      margin: 0 15px;
    }
    &__wrap {
      padding: 25px 30px;
      @media (max-width: $md-extra) {
        padding: 25px 20px;
      }
    }
  }
  &__bucket {
    margin-right: 30px;

    &.myAccount {
      margin-left: auto;
      order: 4;
    }
    @media (max-width: $xl) {
      order: 3;
    }
  }
  &__userMenu {
    &.myAccount {
      order: 5;
    }
    @media (max-width: $xl) {
      width: 12%;
      order: 4;
    }
    @media (max-width: $sm-extra) {
      width: 15%;
    }
  }
  &__submenu {
    font-family: $roboto;
    z-index: 20;
    background: $whiteBg;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    width: 100vw;
    box-shadow: 0 5px 12px -2px #9a9a9a29;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
    &.show-submenu {
      opacity: 1;
      visibility: visible;
      transition: 0.5s;
    }
    & ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 75%;
      margin: auto;

      @media (max-width: $xl) {
        margin-top: 0;
        font-size: 13px;
      }
      @media (max-width: $lg) {
        font-size: 12px;
      }
    }
    &-item {
      text-align: center;
      width: 25%;
      transition: 0.5s;
      &:hover {
        background: $submenuBg;
      }
      &-wrap {
        margin: 10%;

        @media (min-width: 2100px) {
          margin: 10% 20%;
        }
        @media (max-width: $xl) {
          margin: 9%;
        }
      }
      & a {
        text-decoration: none;
      }
    }
    &-image-wrapper {
      display: flex;
      margin: auto;
      justify-content: center;
      align-items: center;
      width: 65px;
      height: 70px;
    }
    &-title {
      font-weight: 400;
      font-size: 17px;
      color: $blackText;
      margin: 5% auto;
    }
    &-description {
      color: $grayText;
      margin-bottom: 5%;
      height: 48px;
      font-weight: 300;
      font-size: 16px;
      line-height: 18px;
    }
  }
}
