@import '../../../../@theme/index';

.details {
  width: 100%;
  box-sizing: border-box;
  &__wrap {
    margin: 30px 35px;
  }
  &__title {
    font-family: $roboto;
    font-weight: 400;
    font-size: 24px;
    color: $blueText;
    margin-bottom: 30px;
  }
  &__userData {
    &__editFields,
    &__changePass,
    &__deleteField {
      border-top: 1px solid $borderHeaderPayment;
      border-left: 1px solid $borderHeaderPayment;
      border-right: 1px solid $borderHeaderPayment;
      box-sizing: border-box;
      -webkit-box-shadow: 0px 0px 9px 2px rgba(111, 120, 128, 0.13);
      -moz-box-shadow: 0px 0px 9px 2px rgba(111, 120, 128, 0.13);
      box-shadow: 0px 0px 9px 2px rgba(111, 120, 128, 0.13);
    }
    &__changePass {
      margin-top: 20px;
    }
    &__deleteField {
      margin-top: 15%;
    }
  }
}
