@import '../../../@theme/index';

.paymentMethod {
  overflow: auto;
  @media (max-width: $lg) {
    border-bottom: 1px solid $accountSettingsBtnBorder;
  }
  &__wrap {
    margin: 30px 40px;
  }
  &__header {
    font-family: $roboto;
    padding-bottom: 20px;
    border-bottom: 1px solid #e2e2e2;
    &__title {
      font-weight: 500;
      font-size: 18px;
      color: $headerSummText;
    }
    &__backBtn {
      font-weight: 400;
      font-size: 16px;
      color: $blueText;
      cursor: pointer;
      text-decoration: underline;
      margin-top: 20px;
      display: flex;
      align-items: center;
    }
    &__backImg {
      margin-right: 3px;
    }
  }
  &__chooseMethod {
    width: 100%;
    &__creditCard {
      border-bottom: 1px solid #e2e2e2;
      &__wrap {
      }
      &__information {
        width: 90%;
        margin: 20px 0 20px 7.5%;
        font-family: $roboto;
        position: relative;
      }
      &__title {
        font-weight: 500;
        font-size: 16px;
        line-height: 18px;
        color: $headerSummText;
        margin-bottom: 5px;
        &.disabledText {
          color: $textAreaCounterText;
        }
      }
      &__text {
        font-weight: 400;
        font-size: 14px;
        color: $textAreaCounterText;
        width: 80%;
      }
      &__image {
        position: absolute;
        top: 0;
        left: -35px;
      }
    }
  }
  &__buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    & button {
      margin-left: 20px;
    }

    &__continue {
      color: $whiteText;
      background: $blueText;
    }
  }
}
