@import "../../../@theme/index";

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__image {
    display: block;
    margin: auto 0;
    max-height: 32px;
    max-width: 37px;
    height: 100%;
    object-fit: contain;
    @media (max-width: $lg) {
      max-height: 25px;
      max-width: 30px;
    }
    @media (max-width: $md-extra) {
      max-height: 20px;
      max-width: 25px;
    }
    @media (max-width: $sm-extra) {
      max-width: 30px;
      max-height: 26px;
    }
  }
  &__title {
    display: block;
    font-family: $archivo;
    text-transform: uppercase;
    color: $blackText;
    font-size: 20px;
    font-weight: bold;
    margin-left: 5px;
    @media (max-width: $lg) {
      font-size: 16px;
    }
    @media (max-width: $md-extra) {
      font-size: 14px;
    }
    @media (max-width: $sm-extra) {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
    }
  }
  a {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
