@import '../../../@theme/index';

.paymentSummary {
  position: relative;
  width: 33%;
  @media (max-width: $xxl) {
    width: 33%;
  }
  @media (max-width: $xl) {
    width: 100%;
    position: relative;
    margin-top: 20px;
  }
  @media (max-width: $sm-extra) {
    position: relative;
    width: 100%;
  }
  &__wrap {
    margin: 30px 30px 40px;
    @media (max-width: $xxl) {
      margin: 10px 15px 15px;
    }
    @media (max-width: $lg) {
      margin: 20px 20px 30px;
    }
    @media (max-width: $sm-extra) {
      margin: 20px;
    }
  }
  &__title {
    font-family: $roboto;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: $darkGrayText;
    margin-bottom: 25px;
    text-transform: uppercase;
    @media (max-width: $xxl) {
      font-size: 16px;
      line-height: 18px;
      font-weight: 500;
      margin-bottom: 10px;
    }
    @media (max-width: $lg) {
      font-size: 18px;
      line-height: 22px;
    }
    @media (max-width: $sm-extra) {
      font-size: 22px;
      line-height: 30px;
      text-transform: capitalize;
      margin-bottom: 15px;
    }
  }
  &__totalSponsorship {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    font-family: $roboto;
    color: $sponsorText;
    font-weight: 400;
    line-height: 22px;
    padding-bottom: 20px;
    border-bottom: 1px solid $borderHeaderPayment;
    margin-bottom: 30px;
    @media (max-width: $xxl) {
      margin-bottom: 15px;
      padding-bottom: 10px;
    }
    @media (max-width: $lg) {
      margin-bottom: 30px;
      padding-bottom: 20px;
    }
    @media (max-width: $sm-extra) {
      margin-bottom: 10px;
    }
    &__title {
      font-size: 16px;
      @media (max-width: $xxl) {
        font-size: 14px;
      }
      @media (max-width: $lg) {
        font-size: 16px;
        line-height: 20px;
      }
      @media (max-width: $sm-extra) {
        font-size: 14px;
        line-height: 20px;
      }
    }
    &__price {
      font-size: 18px;     
      @media (max-width: $xxl) {
        font-size: 16px;
      }
      @media (max-width: $lg) {
        font-size: 18px;
      }     
    }        
    &__currency {
      margin: 0 5px;
      text-transform: uppercase;
    }
  }
  &__toPay {
    display: flex;
    justify-content: flex-end;
    align-items: baseline;
    font-family: $roboto;
    line-height: 22px;
    margin-bottom: 35px;
    @media (max-width: $xxl) {
      margin-bottom: 20px;
    }

    @media (max-width: $sm-extra) {
      justify-content: space-between;
    }
    &.addBottomBorder {
      margin-bottom: 0px;
    }
    &__title {
      font-weight: 400;
      font-size: 16px;
      color: $mainPageCategories;
      text-transform: uppercase;
      margin-right: 20px;
      @media (max-width: $xxl) {
        font-size: 14px;
        margin-right: 18px;
      }
      @media (max-width: $sm-extra) {
        font-size: 18px;
        line-height: 24px;
        color: #333333;
      }
    }
    &__price {
      font-weight: 500;
      font-size: 30px;
      color: $darkGrayText;
      @media (max-width: $xxl) {
        font-size: 24px;
      }
      @media (max-width: $sm-extra) {
        font-size: 22px;
        line-height: 30px;
        color: #333333;
      }
    }  
    &__currency {
      font-size: 20px;
      margin: 0 5px;
      @media (max-width: $xxl) {
        font-size: 16px;
      }
    }
  }
  &__sponsorBtn {
    width: 100%;
    margin-bottom: 30px;
    @media (max-width: $xxl) {
      margin-bottom: 15px;
    }
    @media (max-width: $xl) {
      width: 50%;
      margin: auto;
    }
    & button {
      width: 100%;
      margin-bottom: 20px;
      @media (max-width: $sm-extra) {
        text-transform: initial !important;
      }
    }
  }
  &__agreementText {
    text-align: center;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    color: $grayText;
    @media (max-width: $sm-extra) {
      font-size: 12px;
      line-height: 16px;
      color: $blackText;
    }
  }
  &__details {
    position: relative;
    border-top: 1px solid $borderHeaderPayment;

    font-family: $roboto;
    &__title {
      font-weight: 500;
      font-size: 16px;
      color: $darkGrayText;
    }
    &__dataPairs {
      display: flex;
      width: 100%;

      flex-wrap: wrap;
    }
    &__onePair {
      width: 100%;
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: $xxl) {
        margin-top: 10px;
      }
    }
    &__key {
      font-weight: 300;
      font-size: 14px;
      color: $grayText;
    }
    &__value {
      font-size: 16px;
      font-weight: 400;
      color: $beASponsorThirdBlockDescriptionText;
      @media (max-width: $xxl) {
        font-size: 13px;
        font-weight: 500;
      }
      @media (max-width: $lg) {
        font-size: 16px;
        font-weight: 400;
      }
      @media (max-width: $sm-mini) {
        font-size: 14px;
        font-weight: 500;
      }
    }
  }
  &__differentPayWay {
    font-family: $roboto;
    cursor: pointer;
    text-decoration: underline;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: $blueText;
    margin-top: 20px;
    @media (max-width: $lg) {
      text-align: center;
      text-decoration: none;
      margin-top: 27px;
    }
  }
  &__generateQuote {
    position: absolute;
    top: calc(100% + 15px);
    right: 0;
    @media (max-width: $sm-extra) {
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      margin: 20px auto;
    }
    & button {
      @media (max-width: $sm-extra) {
        color: $submenuLinksText;
      }
    }
  }
}

.border {
  -webkit-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  -moz-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  border: 1px solid $bgMain;
  @media (max-width: $lg) {
    box-shadow: none;
    border: none;
    border-bottom: 1px solid $bgMain;
  }
}
