@import '../../@theme/index';

.categories {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
  cursor: pointer;
  border: 0.5px solid #e1e1e1;
  box-sizing: border-box;
  border-bottom: 0;
  border-right: 0;
  background: #fff;
}
