@import '../../../@theme/index';

.category {
  position: relative;
  width: 33.333333%;
  max-height: 165px;
  height: 100%;
  list-style: none;
  border-right: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  box-sizing: border-box;
  transition: 0.5s;
  @media (max-width: $sm-extra) {
    height: 80px;
  }
  &:hover > &__cross {
    width: 53%;
    height: 53%;
    border-color: $blueText;
    z-index: 9;
  }

  &__cross {
    position: absolute;
    height: 15px;
    width: 15px;
    z-index: 1;
    transition: 1.2s;
    &.left-top {
      border-left: 1px solid $mainPageCategories;
      border-top: 1px solid $mainPageCategories;
      top: -1px;
      left: -1px;
    }
    &.left-bottom {
      border-left: 1px solid $mainPageCategories;
      border-bottom: 1px solid $mainPageCategories;
      bottom: -1px;
      left: -1px;
    }
    &.right-top {
      border-right: 1px solid $mainPageCategories;
      border-top: 1px solid $mainPageCategories;
      top: -1px;
      right: -1px;
    }
    &.right-bottom {
      border-right: 1px solid $mainPageCategories;
      border-bottom: 1px solid $mainPageCategories;
      bottom: -1px;
      right: -1px;
    }
  }

  &__image {
    margin: 32px 27px;
    @media (max-width: $md) {
      text-align: center;
    }
    @media (max-width: $sm-extra) {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: auto 10%;
      height: 100%;
    }
    & img {
      object-fit: contain;
      width: 90%;
      height: 90%;
    }
    &__wrap {
      width: 50px;
      height: 50px;
      @media (max-width: $sm-extra) {
        width: 40px;
        height: 40px;
      }
    }
  }
  &__title {
    font-family: $roboto;
    color: $headerSummText;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: bold;
    margin: 5% 0;
    height: 36px;
    @media (max-width: $lg) {
      font-size: 16px;
    }
    @media (max-width: $md-extra) {
      font-size: 14px;
    }
    @media (max-width: $sm-extra) {
      margin: 0 5%;
      height: auto;
      text-align: left;
    }
  }
  &__description {
    position: absolute;
    top: 20%;
    right: 10%;
    color: $blueText;
    font-family: $roboto;
    font-weight: 400;
    font-size: 16px;
    @media (max-width: $lg) {
      font-size: 13px;
      top: 15%;
      right: 5%;
    }
  }
}

.categories__count- {
  &1 {
    width: 100%;
    margin: 0;
    text-align: center;
  }
  &2 {
    width: 50%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &3 {
    width: 33.33%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &4 {
    width: 25%;
    @media (max-width: $md) {
      width: 50%;
    }
    @media (max-width: $sm) {
      width: 100%;
    }
  }
  &5:nth-child(-n + 3) {
    width: 33.33%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &5:not(:nth-child(-n + 3)) {
    width: 50%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &7:nth-child(-n + 4) {
