  }
  &13:nth-child(9) {
    width: 33.33%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &13:nth-child(10) {
    width: 33.33%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &13:nth-child(11) {
    width: 33.33%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &13:not(:nth-child(-n + 11)) {
    width: 50%;
    @media (max-width: $sm-mini) {
      width: 100%;
    }
  }
  &14:nth-child(-n + 4) {
