@import '../../../@theme/index';

.reasonsForSponsoring {
  padding: 60px 0 70px;
  background: $bgMain;
  &.white {
    background: $whiteText;
  }
  @media (max-width: $xl) {
    padding: 150px 0 70px;
  }
  @media (max-width: $sm-mini) {
    padding: 70px 0 50px;
  }
  // background: $bgMain;
  &__wrap {
    font-family: $roboto;
    text-align: center;
    @media (max-width: $sm-mini) {
      width: 70%;
      margin: auto;
    }
  }
  &__title {
    font-weight: 400;
    font-size: 33px;
    color: $darkestGrayText;
    @media (max-width: $sm-mini) {
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
    }
  }
  &__description {
    font-size: 21px;
    font-weight: 300;
    color: $darkGrayText;
    margin: 25px auto 45px;
    width: 47.5%;
    @media (max-width: $sm-mini) {
      margin: 10px auto 20px;

      font-size: 12px;
      line-height: 16px;
      text-align: left;
      font-weight: normal;
    }
  }
  &__ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: auto;
    @media (max-width: $lg) {
      flex-wrap: wrap;
    }
  }
}
