@import '../../../@theme/index';

.progressBar {
  width: 100%;
  &__points {
    position: relative;
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    @media (max-width: $lg) {
      justify-content: center;
    }
    &__onePoint {
      width: 33.33333%;
      position: relative;
      @media (max-width: $lg) {
        width: calc(50% - 10px);
        &:last-child {
          width: 20px;
        }
      }
      &:not(:last-child)::before {
        content: '';
        position: absolute;
        background: $paymentProgressBarGray;
        height: 2px;
        width: 100%;
        transform: translate(-50%, 0%);
        left: calc(50% + 20px);
        top: 50%;
      }
      &:not(:last-child)::after {
        content: '';
        transition: 1s;
        position: absolute;
        background: $blueText;
        height: 2px;
        width: 0%;
        left: 20px;
        top: 50%;
      }
      &.stepDone {
        &:not(:last-child)::after {
          content: '';
          transition: 1s;
          position: absolute;
          background: $blueText;
          height: 2px;
          width: 100%;
          left: 20px;
          top: 50%;
        }
      }

      &__step {
        position: relative;
        width: 20px;
        height: 20px;
        transition: 1s;

        &.currentStep {
          &::after {
            content: '';
            transition: 1s;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%;
            width: 14px;
            height: 14px;
            background: $blueText;
            border-radius: 50%;
            box-sizing: border-box;
            @media (max-width: $sm-extra) {
              width: 10px;
              height: 10px;
            }
          }
        }
        &.stepDone {
          &::before {
            transition: 1s;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            background: white;
            border: 1px solid $blueText;
            border-radius: 50%;
            box-sizing: border-box;
          }
          &::after {
            content: '';
            transition: 1s;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%;
            width: 14px;
            height: 14px;
            background: $blueText;
            border-radius: 50%;
            box-sizing: border-box;
            @media (max-width: $sm-extra) {
              width: 10px;
              height: 10px;
            }
          }
        }

        &::before {
          content: '';
          transition: 1s;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background: white;
          border: 1px solid $paymentProgressBarGray;
          border-radius: 50%;
          box-sizing: border-box;
        }
        &::after {
          content: '';
          transition: 1s;
          position: absolute;
          transform: translate(-50%, -50%);
          top: 50%;
          left: 50%;
          width: 14px;
          height: 14px;
          background: $paymentProgressBarGray;
          border-radius: 50%;
          box-sizing: border-box;
          @media (max-width: $sm-extra) {
            width: 10px;
            height: 10px;
          }
        }
      }
    }
  }
  &__titles {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin-top: 15px;
    text-align: center;
    @media (max-width: $lg) {
      width: 100%;
    }
    &__text {
      font-family: $roboto;
      font-weight: 400;
      font-size: 15px;
      color: $beASponsorThirdBlockDescriptionText;
      @media (max-width: $sm-extra) {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
      }
    }
  }
}
