@import '../../../@theme/index';

.countriesList {
  width: 660px;
  position: fixed;
  top: 50%;
  background: white;
  transform: translate(-50%, -50%);
  left: 50%;
  z-index: 100;
  padding: 20px;
  box-sizing: border-box;
  @media (max-width: $lg) {
    width: 600px;
  }
  @media (max-width: $md) {
    width: 550px;
  }
  @media (max-width: $sm-extra) {
    width: 100%;
  }
  &.fourthColumn {
    width: 860px;
    @media (max-width: $xl) {
      width: 760px;
    }
    @media (max-width: $lg) {
      width: 560px;
    }
    @media (max-width: $md) {
      width: 550px;
    }
    @media (max-width: $sm-extra) {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    justify-content: space-around;
    @media (max-width: $sm-extra) {
      flex-wrap: wrap;
      justify-content: flex-end;
    }
  }
  &__column {
    width: 27%;
    margin: 0 1.5%;
    @media (max-width: $sm-extra) {
      width: 30%;
    }
  }
  &__element {
    &:first-child {
      margin: 0;
    }
    margin: 15px 0;
  }
  &__countries {
    list-style: none;
    border-bottom: 1px solid $borderCountriesList;
    font-weight: 300;
  }
  &__title {
    font-family: $roboto;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 15px;
    color: $blackText;
    margin-bottom: 15px;
    @media (max-width: $sm-extra) {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }
  }
  &__letter {
    font-family: $roboto;
    font-weight: 400;
    font-size: 15px;
    color: $blueText;
    text-transform: uppercase;
    @media (max-width: $sm-extra) {
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__singleCountry {
    font-family: $roboto;
    font-size: 14px;
    color: $blackText;
    margin: 10px 0;
    @media (max-width: $md) {
      font-size: 12px;
    }
    @media (max-width: $sm-extra) {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
    }
  }
}
