@import '../../../../@theme/index';

.manageInvites {
  width: 100%;
  box-sizing: border-box;
  &__wrap {
    margin: 30px 35px;
  }
  &__title {
    font-family: $roboto;
    font-weight: 400;
    font-size: 24px;
    color: $blueText;
    margin-bottom: 30px;
  }
  &__table {
    width: 100%;
    border-top: 2px solid $borderHeaderPayment;
    &__wrap {
      width: 90%;
      margin: auto;
    }
    &__header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: nowrap;
      border-bottom: 1px solid $borderHeaderPayment;
      &__item {
        padding: 20px 15px 10px;
        box-sizing: border-box;
        width: 50%;
        font-family: $roboto;
        font-weight: 500;
        text-transform: uppercase;
        font-size: 13px;
        line-height: 24px;
        color: $accountSettingsTableHeaderText;
      }
    }
    &__body {
      &__item {
        width: 100%;
        display: flex;
        align-items: center;
        font-family: $roboto;
        padding: 25px 15px 15px;
        box-sizing: border-box;
        border-bottom: 1px solid $borderHeaderPayment;
        font-family: $roboto;
        &__personData {
          width: 50%;
        }
        &__fullname {
          font-weight: 400;
          font-size: 14px;
          color: $blackText;
          margin-bottom: 5px;
        }
        &__department {
          font-weight: 400;
          font-size: 13px;
          color: $squareBenefitText;
        }
        &__date {
          position: relative;
          padding-left: 15px;
          padding-right: 75px;
          box-sizing: border-box;
          font-family: $roboto;
          font-weight: 500;
          font-size: 14px;
          color: $squareBenefitText;
          &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            width: 1px;
            background: $borderHeaderPayment;
          }
        }
        &__controls {
          margin-left: auto;
          display: flex;
          align-items: center;
          &__button {
            background: $whiteBg;
            font-family: $roboto;
            font-weight: 500;
            font-size: 14px;
            color: $mainPageCategories;
            outline: none;
            border: none;
            padding: 5px 5px 5px 25px;
            box-sizing: border-box;
            position: relative;
            &:hover {
              color: $squareBenefitText;
              background: $btnLightGrayBg;
            }
            &.remind {
              &::after {
                content: '';
                position: absolute;
                background: url(../../../../assets/images/ui-icons/blue-ring.png);
                width: 15px;
                background-size: contain;
                height: 15px;
                transform: translate(-50%, -50%);
                top: 50%;
                left: 13px;
              }
            }
            &.rescind {
              transition: 0.3s;

              &::after {
                content: '';
                position: absolute;
                transition: 0.2s;

                background: url(../../../../assets/images/ui-icons/black-close.png);
                width: 10px;
                background-size: contain;
                height: 10px;
                transform: translate(-50%, -50%);
                top: 50%;
                left: 15px;
                box-sizing: border-box;
              }
              &:hover {
                transition: 0.3s;
                &::after {
                  content: '';
                  position: absolute;
                  transition: 0.3s;

                  background: url(../../../../assets/images/ui-icons/red-close.png);
                  width: 10px;
                  background-size: contain;
                  height: 10px;
                  transform: translate(-50%, -50%);
                  top: 50%;
                  left: 15px;
                  box-sizing: border-box;
                }
              }
            }
          }
          &__lastReminded {
            font-style: italic;
            font-size: 14px;
            color: $mainPageCategories;
            margin-right: 10px;
            display: flex;
            align-items: center;
            &__tick {
              margin-right: 5px;
              & img {
                width: 15px;
                height: 15px;
                object-fit: contain;
              }
            }
          }
        }
      }
    }
  }
}
