@import '../../../@theme/index';

.footer {
  width: 75%;
  margin: auto;
  @media (max-width: $sm-extra) {
    width: 100%;
  }
  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: $footerText;
    font-weight: 300;
    height: 10vh;
    font-family: $roboto;
    font-size: 18px;
    padding: 0 30px;

    //media for __row
    @media (max-width: $xl) {
      font-size: 16px;
    }
    @media (max-width: $lg) {
      font-size: 14px;
    }
    @media (max-width: $sm-extra) {
      height: 70px;
      padding: 0 15px;
      font-style: normal;
      font-weight: normal;
      flex-wrap: wrap;
    }
    // end __row media

    &-copyright {
      width: 30%;
      @media (max-width: $sm-extra) {
        width: 100%;
        text-align: center;
        order: 2;
      }
    }
    &-menu {
      order: 1;
      color: #464646;
      line-height: 20px;
      list-style: none;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: $sm-extra) {
        width: 100%;
      }
      &-link {
        a {
          text-decoration: none;
          color: $footerText;
          margin: 0 20px;
          @media (max-width: $md-extra) {
            margin: 0 10px;
          }
          @media (max-width: $sm-extra) {
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
          }
          @media (max-width: $sm-extra) {
            margin: 0;
          }
        }
      }
    }
  }
}
