@import '../../../@theme/index';

.communityHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-family: $roboto;
  border: 1px solid #ebebeb;
  height: 175px;
  background: url(../../../assets/images/SupportedBg.png) no-repeat;
  background-position: right bottom;
  background-size: contain;
  &.withoutTopBorder {
    border-top: none;
  }
  @media (max-width: $sm-extra) {
    border: none;
    background: none;
    height: 54px;
    border-bottom: 1px solid $accountSettingsBtnBorder;
    padding-bottom: 24px;
  }
  &__block {
    & img {
      margin: 0 5%;
    }
    &__first {
      width: 49%;
      display: flex;
      align-items: center;
      &__text {
        color: $blackText;
        width: 80%;
        text-transform: uppercase;
        font-size: 30px;
        @media (max-width: $lg) {
          font-size: 16px;
        }
        @media (max-width: $lg) {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
        }
      }
      &__image {
        object-fit: contain;
        max-width: 105px;
        @media (max-width: $lg) {
          max-width: 70px;
        }
        @media (max-width: $sm-extra) {
          max-width: 49px;
        }
      }
    }
    &__second {
      display: flex;
      align-items: center;
      width: 40%;
      margin-left: 10%;
      @media (max-width: $lg) {
        width: 45%;
        margin-left: 0%;
      }
      @media (max-width: $sm-extra) {
        justify-content: flex-end;
        width: 48%;
      }
      &__text {
        color: $headerSummText;
        font-size: 18px;
        text-transform: uppercase;
        @media (max-width: $lg) {
          font-size: 13px;
        }
        @media (max-width: $lg) {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          text-transform: unset;
        }
      }
      &__image {
        object-fit: contain;
        @media (max-width: $sm-extra) {
          height: 54px;
          width: 54px;
        }
      }
    }
  }
}
