@import '../../../@theme/index';

.bucket {
  &__image {
    display: flex;
    position: relative;
    & img {
      margin: auto;
    }
  }
  &__counter {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    right: -10px;
    background-color: $blueText;
    border-radius: 50%;
    border: 1px solid $whiteText;
    text-align: center;
    color: $whiteText;
    width: 16px;
    height: 16px;
    font-size: 10px;
    @media (max-width: $sm-extra) {
      top: 0%;
      right: -20%;
      width: 16px;
      height: 16px;
    }
  }
}
