@import '../../../../@theme/index';

.editBillingAddress {
  width: 100%;
  &__inputsField {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    flex-wrap: wrap;
  }
  &__label {
    display: block;
    font-family: $roboto;
    font-size: 16px;
    font-weight: 500;
    color: $darkestGrayText;
    margin-bottom: 10px;
    &__required {
      color: $requiredFormFieldText;
    }
    &__optional {
      color: $grayText;
    }
  }

  &__input {
    width: 50%;
    margin-right: 50%;
    margin-bottom: 15px;
    & input {
      width: 100%;
      height: 40px;
      padding: 15px 20px;
      box-sizing: border-box;
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      color: $blackText;
      border: 1px solid $btnLightGrayBg;
      border-radius: 4px;
      &:focus {
        outline: none;
        border-color: $blueText;
      }
    }
  }

  &__buttons {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    &__btn {
      font-family: $roboto;
      font-size: 14px;
      font-weight: 500;
      border: 1px solid transparent;
      height: 30px;
      box-sizing: border-box;
      &:disabled {
        background: $btnDisabledBg;
        color: $mainPageCategories;
      }
      &__cancel {
        color: $darkGrayText;
        background: $btnLightGrayBg;
        border: 1px solid $borderSignOutBtn;
        margin-right: 20px;
        width: 90px;
      }
      &__save {
        color: $whiteText;
        background: $btnBlueBg;
        width: 125px;
      }
    }
    &__cancel {
      margin-right: 20px;
    }
  }
}
