@import "./variables";

@mixin btn() {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 24px 16px;
  font-family: $roboto;
  font-weight: 300;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  outline: none;
  border: 1px solid transparent;
  transition: 0.5s;
  border-radius: 30px;
  -webkit-box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  -moz-box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  &:focus {
    box-shadow: none;
  }

  &:hover {
    box-shadow: none;
  }

  &[disabled] {
    cursor: default;
    opacity: 0.5;
    color: #fff;

    &:hover,
    &:focus {
      color: #fff;
      box-shadow: none;
    }
  }
}

@mixin scrollbars(
  $size,
  $foreground-color,
  $background-color: mix($foreground-color, white, 50%)
) {
  // For Google Chrome
  &::-webkit-scrollbar {
    width: $size;
    height: $size;
  }

  &::-webkit-scrollbar-thumb {
    background: $foreground-color;
  }

  &::-webkit-scrollbar-track {
    background: $background-color;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }

  // For Internet Explorer
  & {
    scrollbar-face-color: $foreground-color;
    scrollbar-track-color: $background-color;
  }
}

@mixin h1 {
  font-size: 40px;
  font-family: $robotoSlab;
  text-align: center;
  font-weight: 700;
  color: $darkGrayText;
  @media (max-width: $lg) {
    font-size: 26px;
  }
  @media (max-width: $md) {
    font-size: 24px;
  }
  @media (max-width: $sm-extra) {
    font-size: 22px;
  }
  @media (max-width: $sm-mini) {
    font-size: 18px;
  }
}
