@import '../../@theme/index';

.contactPage {
  width: 70%;
  margin: auto;
  display: flex;
  flex-wrap: nowrap;
  -webkit-box-shadow: 4px 5px 10px 0px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: 4px 5px 10px 0px rgba(0, 0, 0, 0.16);
  box-shadow: 4px 5px 10px 0px rgba(0, 0, 0, 0.16);
  @media (max-width: $md-extra) {
    width: 100%;
    flex-wrap: wrap;
  }
  &__leftBlock {
    width: 55%;
    background: url(../../assets/images/Contacts/ContactsBG.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    @media (max-width: $md-extra) {
      width: 100%;
      background-position: bottom;
      background-size: cover;
    }

    &__wrap {
      width: 60%;
      margin: 15% 25% 45% 15%;
      @media (max-width: $md-extra) {
        width: 75%;
        margin: 5% auto;
      }
      @media (max-width: $sm) {
        width: 90%;
      }
    }
    &__title {
      font-family: $bitter;
      font-weight: bold;
      font-size: 52px;
      line-height: 58px;
      color: $whiteText;
      text-align: left;
      margin-bottom: 50px;
      @media (max-width: $md-extra) {
        font-size: 38px;
        line-height: 40px;
        margin-bottom: 30px;
      }
      @media (max-width: $sm) {
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 16px;
      }
      @media (max-width: $sm-mini) {
        font-size: 18px;
        line-height: 26px;
      }
    }
    &__text {
      font-family: $roboto;
      font-size: 21px;
      line-height: 34px;
      font-weight: 300;
      color: $contactsText;
      width: 95%;
      @media (max-width: $md-extra) {
        font-size: 18px;
        line-height: 26px;
      }
      @media (max-width: $sm) {
        font-size: 14px;
        line-height: 20px;
      }
      @media (max-width: $sm-mini) {
        font-size: 12px;
        line-height: 16px;
      }
    }
  }
  &__rightBlock {
    width: 45%;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: $md-extra) {
      margin-top: 5%;
      width: 100%;
    }
    &__wrap {
      width: 70%;
      @media (max-width: $md-extra) {
        width: 80%;
      }
      @media (max-width: $sm) {
        width: 90%;
      }
    }
    &__inputWrap {
      width: 100%;
      margin-bottom: 35px;
      position: relative;
      & input {
        height: 50px;
      }
    }
    &__selectInput {
      display: flex;
      flex-wrap: nowrap;
      position: relative;

      &__select {
        width: 20%;
      }
      &__countryCode {
        width: 10%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border-top: 1px solid $loginBorder;
        border-bottom: 1px solid $loginBorder;
        font-family: $roboto;
        font-weight: 400;
        font-size: 16px;
        color: $blackText;
      }
      &__input {
        width: 70%;
      }
    }
    &__label {
      font-family: $roboto;
      font-weight: 400;
      font-size: 17px;
      color: $darkestGrayText;
      display: block;
      @media (max-width: $sm) {
        font-size: 14px;
      }
    }

    &__input {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
      height: 100%;
      font-family: $roboto;
      font-size: 16px;
      color: $blackText;
      box-sizing: border-box;
      padding-left: 20px;
      border: 1px solid $loginBorder;
      border-radius: 2px;
      -webkit-appearance: none;
      @media (max-width: $sm) {
        font-size: 18px;
      }
      &.inputWithSelect {
        border-left: none;
        padding-left: 0px;
      }
      &::placeholder {
        font-weight: 400;
        color: $mainPageCategories;
        font-size: 16px;
        @media (max-width: $sm) {
          font-size: 18px;
        }
      }
      &:focus {
        outline: none;
      }
    }
    &__errorText {
      position: absolute;
      bottom: -25%;
      left: 0;
      font-family: $roboto;
      font-weight: 400;
      font-size: 14px;
      color: $inputErrorText;
      @media (max-width: $sm-mini) {
        font-size: 12px;
      }
    }
    &__select {
      height: 50px;
      width: 100%;
      border: 1px solid $loginBorder;
      border-right: none;

      &:focus {
        outline: none;
      }
    }
    &__selectImage {
      // margin-left: 10px;
    }
    &__labelRedColor {
      color: $requiredFormFieldText;
    }
    &__textArea {
      width: 100%;
      position: relative;
      & textarea {
        width: 100%;
        height: 115px;
        resize: none;
        border: 1px solid $loginBorder;
        padding: 10px 20px;
        box-sizing: border-box;
        font-family: $roboto;
        font-weight: 400;
        font-size: 16px;
        color: $blackText;
        @media (max-width: $sm) {
          font-size: 18px;
        }
        &::placeholder {
          color: $sponsorText;
          font-size: 16px;
          @media (max-width: $sm) {
            font-size: 18px;
          }
        }
        &:focus {
          outline: none;
        }
      }
      &__symbolsCounter {
        position: absolute;
        bottom: 10px;
        right: 10px;
        font-family: $roboto;
        font-weight: 400;
        font-size: 12px;
        color: $textAreaCounterText;
      }
    }
    &__confirmBtn {
      width: 100%;
      margin-top: 30px;
      &__wrap {
        width: 70%;
        margin: auto;
        @media (max-width: $sm) {
          width: 50%;
        }

        & button {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-family: $roboto;
          font-weight: 400;
          text-transform: capitalize;
          font-size: 16px;
          color: $whiteText;
          border-radius: 30px;
          background: $btnGreenBg;
          outline: none;
          cursor: pointer;
          border: none;
          padding: 15px 0;
          transition: 0.4s;
          -webkit-box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.2);
          -moz-box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.2);
          &:hover {
            box-shadow: none;
          }
        }
      }
    }
    &__privacy {
      margin-top: 40px;
      width: 100%;
      &__text {
        text-align: center;
        width: 70%;
        margin: auto;
        font-family: $roboto;
        font-size: 18px;
        font-weight: 300;
        color: $blackText;
        & a {
          color: $blueText;
          text-decoration: none;
        }
      }
    }
    &__messageSent {
      width: 100%;
      font-family: $roboto;
      text-align: center;
      &__textBlock {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 60%;
        margin: auto;
        @media (max-width: $md-extra) {
          margin-left: 40%;
        }
      }
      &__title {
        margin-top: 60px;
        font-weight: 300;
        font-size: 32px;
        color: $blackText;
        @media (max-width: $md-extra) {
          margin-top: 0;
        }
        @media (max-width: $sm-mini) {
          font-size: 18px;
          line-height: 24px;
        }
      }
      &__text {
        margin-top: 40px;
        font-weight: 300;
        font-size: 24px;
        line-height: 30px;
        color: $grayText;
        @media (max-width: $md-extra) {
          margin-top: 20px;
        }
        @media (max-width: $sm-mini) {
          font-size: 12px;
          line-height: 16px;
        }
      }
      & img {
        display: block;
        margin: auto;
        object-fit: contain;
        max-width: 100%;
        @media (max-width: $md-extra) {
          margin: 0;
        }
      }
    }
  }
}

.error {
  border-color: $inputErrorBorder;
}
