@import "../../../@theme/index";

.benefit {
  width: 33.333%;
  @media (max-width: $xl) {
    width: 100%;
  }
  &__wrap {
    margin: 40px 50px 50px 30px;
    @media (max-width: $sm-mini) {
      margin: 0;
      margin-top: 32px;
    }
  }
  &__title {
    font-size: 28px;
    margin-bottom: 25px;
    font-weight: 500;
    @media (max-width: $sm-mini) {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 5px;
    }
  }
  &__coloredLine {
    width: 35px;
    height: 3px;
    display: block;
    margin-bottom: 30px;
    border-radius: 2px;
    @media (max-width: $sm-mini) {
      margin-bottom: 5px;
      width: 25px;
    }
  }
  &__text {
    font-family: $roboto;
    font-weight: 300;
    font-size: 18px;
    color: $squareBenefitText;
    line-height: 30px;
    @media (max-width: $sm-mini) {
      line-height: 20px;
      font-size: 14px;
      font-weight: normal;
      color: #828282;
      width: 93%;
    }
  }
  &:nth-child(2) {
    border-left: 1px solid $btnLightGrayBg;
    border-right: 1px solid $btnLightGrayBg;
    @media (max-width: $xl) {
      border: none;
    }
  }
}
