@import '../../../@theme/index';

.input {
  width: 100%;
  margin: 20px 0;
  font-family: $roboto;

  &__wrap {
    position: relative;
  }
  &__label {
    position: absolute;
    top: -26px;
    left: 0;
    font-weight: 500;
  }
  &__input {
    border: 1px solid #e1e1e1;
    border-radius: 2px;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 400;
    width: 100%;
    color: $blackText;
    box-sizing: border-box;
    outline: none;
    &:focus {
      border: 1px solid $blueText;
    }
  }
}
