@import '../../@theme/index';

.signUp {
  width: 100%;
  min-height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  &__wrap {
    margin: 10vh auto;
    width: 25%;

    @media (max-width: $xl) {
      width: 40%;
    }
    @media (max-width: $md) {
      width: 60%;
    }
    @media (max-width: $sm-extra) {
      width: 90%;
    }
  }
  &__error {
    position: absolute;
    margin-top: 10px;
    top: 0;
    width: 100%;
    padding: 15px 25px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background: #f8f8f8;
    opacity: 0;
    visibility: hidden;
    transition: 0.4s;
    width: 25%;
    font-family: $roboto;
    font-size: 16px;
    font-weight: 400;
    border: 1px solid $errorBorder;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 5px;
      background: $errorBorder;
    }
    @media (max-width: $xl) {
      width: 40%;
    }
    @media (max-width: $md) {
      width: 60%;
    }
    @media (max-width: $sm-extra) {
      width: 90%;
    }
    &.show {
      opacity: 1;
      visibility: visible;
    }
    &__message {
      color: $blackText;
      position: relative;
      &__icon {
        position: absolute;
        top: 0;
        left: -30px;
        & img {
          width: 20px;
          object-fit: contain;
        }
      }
      &__close {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        right: -30px;
      }
    }
  }
  &__logo {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    & img {
      width: auto;
      height: 50px;
      object-fit: contain;
    }
  }
  &__title {
    text-transform: capitalize;
    font-family: $roboto;
    font-weight: 300;
    font-size: 30px;
    color: $blackText;
    width: 100%;
    text-align: center;
    @media (max-width: $md) {
      font-size: 28px;
      line-height: 30px;
    }
  }
  &__subtitle {
    font-family: $roboto;
    font-weight: 400;
    font-size: 16px;
    color: $grayText;
    width: 80%;
    text-align: center;
    margin: 0 auto 30px;
  }
  &__finishRegistration {
    margin-top: 50px;
    &__text {
      font-family: $roboto;
      font-weight: 400;
      font-size: 18px;
      line-height: 24px;
      color: $grayText;
      text-align: center;
    }
    &__btn {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-top: 90px;
    }
  }
}
