@import '../../@theme/index';

.sponsor {
  &__header {
    font-family: $roboto;
    font-weight: 300;
    color: $userMenuFullNameText;
    padding: 1%;
    text-align: center;
    border: 1px solid $btnLightGrayBg;
    border-top: none;
    font-size: 20px;
    @media (max-width: $md) {
      font-size: 18px;
      border: none;
    }
    @media (max-width: $sm-extra) {
      margin: 16px auto;
      color: $userMenuFullNameText;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
    }
  }
  &__table {
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid $btnLightGrayBg;
    border-right: 1px solid $btnLightGrayBg;
    @media (max-width: $xxl) {
      flex-wrap: wrap;
    }
    @media (max-width: $md-extra) {
      border: none;
    }
    &-element {
      width: 16.5%;
      text-align: center;
      border-left: 1px solid $btnLightGrayBg;
      border-top: none;
      cursor: pointer;
      @media (max-width: $xxl) {
        width: 33.3%;
        border-bottom: 1px solid $btnLightGrayBg;
        box-sizing: border-box;
      }
      @media (max-width: $md-extra) {
        width: 100%;
        border: 1px solid #e0e0e0;
        border-right: none;
        height: 100%;
      }

      &__wrapper {
        margin: 30px 20px 25px;
        @media (max-width: $xl) {
          margin: 15% 10%;
        }
        @media (max-width: $md-extra) {
          margin: 24px;
        }
      }
      &-region {
        font-family: $roboto;
        font-weight: 500;
        font-size: 19px;
        color: $darkGrayText;
        min-height: 38px;
        margin-bottom: 28px;
        @media (max-width: $md) {
          font-size: 16px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          margin-bottom: 16px;
          min-height: auto;
          line-height: 24px;
        }
      }
      &-countriesCount {
        font-family: $roboto;
        font-weight: bold;
        color: $countriesCountBlueText;
        font-size: 15px;
        @media (max-width: $md) {
          font-size: 14px;
        }
        @media (max-width: $md) {
          font-weight: 500;
          font-style: normal;
          font-size: 14px;
          line-height: 20px;
        }
        // &:hover {
        //   color: $blueText;
        // }
        &-wrapper {
          position: relative;
        }
      }
      &-img-link {
        position: absolute;
        top: 0;
        right: -25%;
      }
      &-description {
        margin-top: 15px;
        font-size: 14px;
        font-family: $roboto;
        font-weight: 500;
        color: $darkestGrayText;
        line-height: 1.3;
        min-height: 72px;
        @media (max-width: $xxl) {
          min-height: 54px;
        }
        @media (max-width: $sm-extra) {
          margin-top: 8px;
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 16px;
          min-height: 48px;
        }
      }
      &-price {
        font-family: $roboto;
        color: $userMenuFullNameText;
        font-size: 26px;
        font-weight: 400;
        margin-top: 20px;
        @media (max-width: $md) {
          font-size: 22px;
        }
        @media (max-width: $sm-extra) {
          margin-top: 22px;
          line-height: 30px;
        }
      }
      &-currency {
        font-size: 21px;
        margin-left: 5px;
        @media (max-width: $md) {
          font-size: 16px;
        }
      }
      &-sponsorsText {
        font-family: $roboto;
        font-weight: 300;
        font-size: 16px;
        color: $submenuLinksText;
        margin: 10% auto 15%;
        @media (max-width: $md) {
          font-size: 14px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 16px;
          margin: 8px auto 24px;
        }

        &-black {
          color: $blackText;
        }
      }
      &-paid {
        font-family: $roboto;
        font-weight: 400;
        font-size: 18px;
        color: $headerSummText;
      }
    }
  }
}

.sponsor__slide {
  &:last-child {
    border-right: 1px solid #e0e0e0;
  }
}
