@import '../../../@theme/index';

.scrollToTop {
  position: fixed;
  top: 85%;
  right: 10%;
  z-index: 9999;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transition: 1s;

  &__show {
    opacity: 1;
    visibility: visible;
  }
  &__image {
    width: 50px;
    height: 50px;
    background: $scrollToTopBtnBG;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    transition: 0.4s;
    &:hover {
      background: #7d7b7b;
    }
    @media (max-width: $sm-extra) {
      width: 35px;
      height: 35px;
    }
    & img {
      width: 40%;
      height: 40%;
      object-fit: contain;
    }
  }
  &__text {
    text-align: center;
    font-family: $roboto;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    color: $grayText;
    @media (max-width: $sm-extra) {
      font-size: 13px;
      line-height: 20px;
    }
  }
}
