@import '../../../../@theme/index';

.cardPayment {
  width: 50%;
  &__wrap {
    margin: 30px 40px;
  }
  &__header {
    margin-bottom: 30px;
    &__title {
      font-family: $roboto;
      font-weight: 500;
      font-size: 18px;
      color: $headerSummText;
    }
  }
  &__cardDetails {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    &__row {
      width: 100%;
      margin-bottom: 20px;
      justify-content: space-between;
      display: flex;
      & label {
        display: block;
        font-family: $roboto;
        font-weight: 500;
        font-size: 15px;
        color: $darkestGrayText;
        margin-bottom: 10px;
      }
      & input {
        height: 40px;
        width: 100%;
        font-family: $roboto;
        font-weight: 300;
        font-size: 18px;
        color: $blackText;
        padding: 15px;
        box-sizing: border-box;
        border: 1px solid $btnDisabledBg;
        border-radius: 3px;
      }
    }

    &__inputWrap {
      position: relative;
    }

    &__image {
      position: absolute;
      transform: translate(0%, -50%);
      top: 50%;
      right: 5px;
    }

    &__cardNumber {
      width: 100%;
    }

    &__expiryDate {
      width: 48%;
    }
    &__cvc {
      width: 48%;
    }
  }
  &__backBtn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    margin-top: 20px;
    cursor: pointer;
    &__img {
      margin-right: 5px;
    }
    &__text {
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      line-height: 26px;
      color: $blueText;
      text-decoration: underline;
    }
  }
  &__button {
    & button {
      width: 100%;
    }
  }
}
