@import '../../../@theme/index';

.firstStep {
  width: 100%;
  margin-top: 25px;
  position: relative;
  &__error {
    position: absolute;
    top: 0;
  }
  &__fullName {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    &__inputField {
      width: 48%;
      margin-bottom: 20px;
    }
  }
  &__inputField {
    width: 100%;
    margin-bottom: 20px;

    &__label {
      font-family: $roboto;
      font-size: 15px;
      font-weight: 500;
      color: $darkestGrayText;
      margin-bottom: 10px;
      display: block;
    }
    &__input {
      position: relative;
      width: 100%;
      & input {
        width: 100%;
        padding: 15px 20px;
        box-sizing: border-box;
        height: 40px;
        border: 1px solid $btnDisabledBg;
        font-family: $roboto;
        font-weight: 400;
        font-size: 16px;
        border-radius: 2px;
        color: $blackText;
        transition: all 0.3s, letter-spacing 0.1s;
        &[type='password'] {
          letter-spacing: 3px;
          transition: 0s;
        }
        &:focus {
          border: 1px solid $blueText;
        }
        &::placeholder {
          letter-spacing: initial;
          color: $grayText;
        }
        &:focus {
          outline: none;
        }
        &.error {
          border-color: $inputErrorBorder;
          transition: 0.3s;
        }
      }
    }
    &__error {
      position: absolute;
      top: 150%;
      left: 0;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
      color: $requiredFormFieldText;
      background: $errorRedBg;
      border: 1px solid $requiredFormFieldText;
      font-family: $roboto;
      font-size: 14px;
      font-weight: 400;
      padding: 0px;
      width: 100%;
      box-sizing: border-box;
      text-align: center;
      &::before {
        content: '';
        display: block;
        border: 8px solid transparent;
        border-bottom-color: $requiredFormFieldText;
        position: absolute;
        top: -50%;
        left: 12px;
      }
      &::after {
        content: '';
        display: block;
        border: 9px solid transparent;
        border-bottom-color: $errorRedBg;
        position: absolute;
        top: calc(-55% + 2px);
        left: calc(10px + 1px);
      }
      &.show {
        opacity: 1;
        visibility: visible;
        top: 120%;
        padding: 10px 15px;
        z-index: 10;
      }
    }
    &__confirmedError {
      position: absolute;
      top: 150%;
      left: 0;
      opacity: 0;
      visibility: hidden;
      color: $requiredFormFieldText;
      font-family: $roboto;
      font-size: 14px;
      font-weight: 400;
      transition: 0.3s;

      &.show {
        opacity: 1;
        visibility: visible;
        top: 115%;
      }
    }
    &__confirmedSuccess {
      position: absolute;
      top: 150%;
      left: 0;
      opacity: 0;
      visibility: hidden;
      font-family: $roboto;
      font-size: 14px;
      font-weight: 400;
      transition: 0.3s;
      color: $btnGreenBg;
      background: $successValidationPrompt;
      border: 1px solid $btnGreenBg;
      &::before {
        content: '';
        display: block;
        border: 8px solid transparent;
        border-bottom-color: $btnGreenBg;
        position: absolute;
        top: -50%;
        left: 12px;
      }
      &::after {
        content: '';
        display: block;
        border: 9px solid transparent;
        border-bottom-color: $successValidationPrompt;
        position: absolute;
        top: calc(-55% + 2px);
        left: calc(10px + 1px);
      }
      &.show {
        opacity: 1;
        visibility: visible;
        top: 120%;
        padding: 10px 15px;
      }
    }
  }
  &__firstStepBtn {
    width: 100%;
    margin: 35px auto;
    display: flex;

    justify-content: center;
    & button {
      width: 100%;
      text-transform: capitalize;
      font-size: 16px;
      font-weight: 400;
    }
  }
  &__agreementText {
    width: 80%;
    margin: auto;
    text-align: center;
    font-family: $roboto;
    font-weight: 400;
    font-size: 14px;
    color: $agreementText;
    line-height: 18px;
    & a {
      color: $blueText;
      text-decoration: none;
    }
  }
  &__hidePassword {
    position: absolute;
    background: url(../../../assets/images/ui-icons/passwordGrayEye.png)
      no-repeat;
    background-size: contain;
    background-position: center;
    transform: translate(-50%, -50%);
    top: 50%;
    right: 2%;
    width: 25px;
    height: 25px;
    @media (max-width: $sm-extra) {
      width: 20px;
      height: 20px;
    }
  }
  &__showPassword {
    position: absolute;
    background: url(../../../assets/images/ui-icons/passwordBlackEye.png)
      no-repeat;
    background-size: contain;
    background-position: center;
    transform: translate(-50%, -50%);
    top: 50%;
    right: 2%;
    width: 25px;
    height: 25px;
    @media (max-width: $sm-extra) {
      width: 20px;
      height: 20px;
    }
  }
  &__checkingPrompt {
    width: 80%;
    margin: 13px 0 18px;
    font-family: $roboto;
    font-weight: 400;
    font-size: 14px;
    color: $sponsorText;
    & a {
      color: $blueText;
      text-decoration: none;
    }
  }
  &__validation {
    &__success {
      color: $btnGreenBg;
    }
    &__error {
      color: $requiredFormFieldText;
    }
  }
  &__confirmedPasswordError {
    position: absolute;
    top: 150%;
    left: 0;
    opacity: 0;
    visibility: hidden;
    color: $requiredFormFieldText;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 400;
    transition: 0.3s;

    &.show {
      opacity: 1;
      visibility: visible;
      top: 115%;
    }
  }
  &__confirmedPasswordSuccess {
    position: absolute;
    top: 150%;
    left: 0;
    opacity: 0;
    visibility: hidden;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 400;
    transition: 0.3s;
    color: $btnGreenBg;
    background: $successValidationPrompt;
    border: 1px solid $btnGreenBg;
    &::before {
      content: '';
      display: block;
      border: 8px solid transparent;
      border-bottom-color: $btnGreenBg;
      position: absolute;
      top: -50%;
      left: 12px;
    }
    &::after {
      content: '';
      display: block;
      border: 9px solid transparent;
      border-bottom-color: $successValidationPrompt;
      position: absolute;
      top: calc(-55% + 2px);
      left: calc(10px + 1px);
    }
    &.show {
      opacity: 1;
      visibility: visible;
      top: 120%;
      padding: 10px 15px;
    }
  }
}
