@import '../../../@theme/index';

.community {
  width: 100%;
  border: 1px solid black;
  margin-top: 15px;
  -webkit-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  -moz-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
  border: 1px solid $bgMain;
  &__wrap {
    padding: 20px 30px;
    box-sizing: border-box;
    width: 100%;
  }
  &__header {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: flex-start;
    @media (max-width: $xl) {
      flex-wrap: wrap;
    }
    &__image {
      width: 55px;
      margin-right: 15px;
      & img {
        object-fit: contain;
      }
    }
    &__data {
      font-family: $roboto;
      &__title {
        font-weight: 700;
        font-size: 18px;
        color: $headerSummText;
        text-transform: uppercase;
      }
      &__campaign {
        margin-top: 10px;
        font-weight: 400;
        font-size: 13px;
        color: $btnGreenBg;
      }
    }
    &__paymentButton {
      margin-left: auto;
      @media (max-width: $xl) {
        width: 100%;
        & button {
          margin: 10px auto 0 auto;
          width: 70%;
          @media (max-width: $sm) {
            width: 100%;
          }
        }
      }
    }
  }
  &__regions {
    margin-bottom: 60px;
    font-family: $roboto;
    &__single {
      padding-left: 70px; //55px image + 15px margin
      box-sizing: border-box;
      border-bottom: 1px solid $btnLightGrayBg;
      margin-top: 25px;
      position: relative;
      &__title {
        font-weight: 500;
        font-size: 16px;
        color: $headerSummText;
        margin-bottom: 10px;
      }
      &__countries {
        margin-bottom: 20px;
        font-weight: 400;
        font-size: 14px;
        &__count {
          color: $blueText;
        }
        &__title {
          color: $textAreaCounterText;
        }
      }
      &__price {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 10px;
        color: $headerSummText;
        &__currency {
          font-size: 20px;
          margin-left: 10px;
        }
      }
      &__program {
        font-weight: 400;
        font-size: 14px;
        margin-bottom: 20px;
        &__pilot {
          color: $headerSummText;
        }
        &__impressions {
          color: $textAreaCounterText;
        }
      }
      &__remove {
        position: absolute;
        top: 10px;
        right: 0;
        cursor: pointer;
      }
    }
  }
}
