@import '../../../@theme/index';

.btn-default {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: $roboto;
  font-size: 16px;
  font-weight: 400;
  border: 1px solid transparent;
  box-shadow: none;
  border-radius: 30px;
  // padding: 15px 30px;
  padding: 10px 30px;
  transition: 0.3s;
  // margin: 20px 0;
  box-sizing: border-box;
  max-height: 40px;
  &:hover {
    box-shadow: 0 5px 12px 0px #9a9a9aad;
  }
  &:disabled {
    background: $loginBorder;
  }
  & img {
    margin-left: 5px;
    margin-right: 5px;
  }
  &.blueBG {
    background: $blueText;
    color: $whiteText;
    &:hover {
      background: $blueBtnHover;
    }
    &:disabled {
      background: $loginBorder;
    }
  }
  &.grayBG {
    background: $btnLightGrayBg;
    border: 1px solid $borderHeaderPayment;
    color: $blackText;
  }
  &.whiteBG__blueBorder {
    background: $whiteText;
    border: 1px solid $blueText;
    color: $blueText;
  }
  &.grayBigBG {
    background: $btnLightGrayBg;
    font-size: 14px;
    font-weight: 400;
    color: $cancelBtnText;
    padding: 15px 25px;
    border-radius: 30px;
  }
  &.uppercase {
    text-transform: uppercase;
  }
}

button,
button:active,
button:focus {
  outline: none;
  cursor: pointer;
}

.btn-sponsor {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: 1px solid $btnBorder;
  width: 67px;
  height: 30px;
  border-radius: 24px;
  margin: auto;
  transition: 0.5s;
  @media (max-width: $sm-extra) {
    width: 65px;
    height: 39px;
  }
  img {
    width: 15px;
    height: 15px;
  }
  & .svg-image {
    display: block;
  }
  .svg-image-hover {
    display: none;
  }
  &:hover {
    background: $blueText;
    .svg-image {
      display: none;
    }
    .svg-image-hover {
      display: block;
    }
  }
  &:disabled {
    background: #cccccc;
    opacity: 0.5;
  }
}
.btn-default-hover {
  background: $blueText;
}

.btn-clicked {
  color: $whiteText;
  background: $btnGreenText;
}

.clicked {
  background: $btnGreenBg;

  &:hover {
    background: $btnMinusHoverGb;
    border: 1px solid $btnBorder;
  }
  & .minus-image {
    height: 4px;
  }
}

.payment {
  font-family: $roboto;
  width: 140px;
  height: 44px;
  background: $blueText;
  color: $whiteText;
  font-size: 16px;
  font-weight: 400;
  border: 1px solid transparent;
  box-sizing: border-box;
  border-radius: 30px;

  @media (max-width: $md) {
    width: 90px;
  }
  @media (max-width: $xxl) {
    width: 100px;
    font-size: 13px;
  }
  &:hover {
    box-shadow: 0 5px 12px 0px #9a9a9aad;
  }
  &:disabled {
    background-color: $btnDisabledBg;
    border: 1px solid $btnBorder;
    color: $sponsorText;
    box-shadow: none;
  }

  @media (max-width: $xxl) {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
  }
  @media (max-width: $sm-extra) {
    width: 110px;
  }
}

button.prePayment {
  max-width: 265px;
  width: 140px;
  height: 40px;
  border-radius: 24px;
  background: $btnLightGrayBg;
  color: $blackText;
  font-size: 16px;
  font-weight: 400;
  border: 1px solid $btnBorder;
  @media (max-width: $xxl) {
    font-size: 13px;
  }
  @media (max-width: $sm-extra) {
    width: 110px;
  }
}

.btn-login {
  font-family: $roboto;
  border-radius: 24px;
  padding: 12px 32px;
  transition: 0.3s;
  background: $blueText;
  color: $whiteText;
  font-size: 16px;
  border: 1px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 5px 12px 0px #9a9a9aad;

  &.centered {
    margin: auto;
  }
  @media (max-width: $sm-extra) {
    width: 104px;
    height: 44px;
    font-weight: 500;
    font-size: 14px;
    padding: 0;
  }
  &:hover {
    box-shadow: none;
  }
  &:disabled {
    background-color: $btnDisabledBg;
    border: 1px solid $btnBorder;
    color: $submenuLinksText;
    box-shadow: none;
    font-style: normal;
    @media (max-width: $sm-extra) {
      font-weight: 500;
      font-size: 14px;
    }
  }
  &:focus {
    border: 1px solid $blackText;
  }
  &-image {
    display: flex;
    justify-content: center;
    align-items: center;
    & img {
      width: 16px;
      height: 16px;
      margin-left: 10px;
      object-fit: contain;
      @media (max-width: $sm-extra) {
        width: 14px;
        height: 11px;
      }
    }
  }
}

.back-button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $roboto;
  border-radius: 24px;
  padding: 12px 24px;
  background: $btnMinusHoverGb;
  font-size: 16px;
  border: 1px solid $borderMinusBtn;
  color: $accountSettingsBtnText;
  font-style: normal;
  font-weight: 500;
  -webkit-box-shadow: 0 5px 12px 0px #9a9a9aad;
  -moz-box-shadow: 0 5px 12px 0px #9a9a9aad;
  box-shadow: 0 5px 12px 0px #9a9a9aad;
  transition: 0.4s;
  &:hover {
    box-shadow: none;
  }
  &:focus {
    border: 1px solid $blackText;
  }
  @media (max-width: $md) {
    font-size: 14px;
  }
  @media (max-width: $sm-mini) {
    font-size: 14px;
    line-height: 20px;
  }

  &-image {
    display: flex;
    justify-content: center;
    align-items: center;
    &__revert {
      //   transform: rotate(180deg);
      margin-left: 10px;
    }
  }
  & img {
    width: 16px;
    height: 16px;
    margin-right: 10px;
    object-fit: contain;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $sm-extra) {
      width: 14px;
      height: 11px;
    }
  }
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $roboto;
  border-radius: 24px;
  width: 164px;
  height: 40px;
  font-size: 16px;
  border: 1px solid transparent;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  -webkit-box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  -moz-box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  box-shadow: 0px 10px 14px 3px rgba(34, 60, 80, 0.16);
  transition: 0.4s;
  text-align: center;
  &:hover {
    box-shadow: none;
  }
  @media (max-width: $md) {
    font-size: 15px;
  }
  @media (max-width: $sm-extra) {
    width: 104px;
    height: 44px;
    padding: 0;
    font-size: 14px;
  }
}

.orangeBG {
  font-size: 18px;
  font-weight: 400;
  background: $redOrangeText;
  color: $whiteText;
  @media (max-width: $sm-mini) {
    font-size: 14px;
    line-height: 20px;
  }
}

.greenBG {
  background: green;
  color: white;
  width: 210px;
  height: 50px;
  font-size: 18px;
  @media (max-width: $sm) {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    width: 160px;
    height: 44px;
  }
}
