@import '../../@theme/index';

.checkout {
  width: 70%;
  margin: auto;
  @media (max-width: $sm-extra) {
    width: 95%;
  }
  &__border {
    -webkit-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
    -moz-box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
    box-shadow: 0px 0px 6px 0px rgba(179, 184, 187, 0.2);
    border: 1px solid $bgMain;
    @media (max-width: $sm-extra) {
      border: 1px solid transparent;
      box-shadow: none;
    }
  }
  &__leftBlock {
    width: 65%;

    @media (max-width: $xl) {
      width: 100%;
    }
  }
  &__wrap {
    width: 100%;
    padding: 30px 40px;
    box-sizing: border-box;
    @media (max-width: $lg) {
      padding: 30px 5%;
      border-bottom: 1px solid $accountSettingsBtnBorder;
      margin: 0;
    }
  }
  &__processBarWrap {
    width: 65%;
    padding: 25px 0px 30px;
    @media (max-width: $lg) {
      width: 100%;
    }
    @media (max-width: $sm-extra) {
      width: 90%;
      margin: 10px auto;
      border-bottom: 1px solid $accountSettingsBtnBorder;
    }
  }
  &__invoice {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 10vh;
    cursor: pointer;
    &__title {
      font-family: $roboto;
      font-size: 18px;
      font-weight: 500;
      line-height: 11px;
      color: $headerSummText;
      margin-bottom: 15px;
    }
    &__textarea {
      width: 68%;
      @media (max-width: $xl) {
        width: 100%;
      }
      @media (max-width: $sm-extra) {
        width: 100%;
      }
      & textarea {
        font-family: $roboto;
        font-weight: 300;
        font-size: 18px;
        line-height: 26px;
        color: $headerSummText;
        height: 140px;
        width: 100%;
        border: 1px solid $textAreaSuccessBorder;
        resize: none;
        outline: none;
        padding: 20px 15px;
        box-sizing: border-box;
        margin-bottom: 15px;
        @media (max-width: $xl) {
          font-size: 16px;
          line-height: 22px;
        }
        @media (max-width: $sm-extra) {
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
    &__changeAddress {
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      line-height: 26px;
      color: $blueText;
      margin-bottom: 30px;
      @media (max-width: $sm-extra) {
        font-size: 14px;
      }
    }
    &__question {
      width: 85%;
      font-family: $roboto;
      @media (max-width: $xl) {
        width: 100%;
      }
      &__title {
        font-weight: 500;
        font-size: 16px;
        line-height: 18px;
        color: $headerSummText;
        margin-bottom: 10px;
      }
      &__description {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: $textAreaCounterText;
        margin-bottom: 30px;
        @media (max-width: $sm-extra) {
          font-size: 14px;
          line-height: 20px;
        }
      }
      &__warningText {
        color: $redHelpText;
      }
    }
  }
  &__changeAddress {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    width: 500px;
    z-index: 100;
    background: $whiteBg;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @media (max-width: $sm) {
      width: 360px;
    }
    @media (max-width: $sm-mini) {
      width: 90vw;
    }
    &__header {
      width: 100%;
      position: relative;
      font-family: $roboto;
      border-bottom: 1px solid $borderHeaderPayment;
      &__title {
        font-weight: 500;
        font-size: 18px;
        padding: 20px 30px 25px;
        box-sizing: border-box;
      }
      &__close {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        right: 30px;
        cursor: pointer;
        & img {
          width: 15px;
          object-fit: contain;
        }
      }
    }
    &__fields {
      width: 100%;
      &__wrap {
        width: 100%;
        padding: 20px 30px 25px;
        box-sizing: border-box;
      }
      &__input {
        width: 100%;
        margin-bottom: 45px;
        &:nth-child(1) {
          margin-bottom: 0;
        }
        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
    &__buttons {
      display: flex;
      justify-content: flex-end;
      &__btn {
        padding: 5px 20px;
        box-sizing: border-box;
        outline: none;
        font-family: $roboto;
        font-weight: 500;
        font-size: 14px;
      }
      &__cancel {
        margin-right: 20px;
        & button {
          color: $darkGrayText;
          background: $btnLightGrayBg;
          border: 1px solid $borderSignOutBtn;
        }
      }
      &__add {
        & button {
          color: $whiteText;
          background: $btnBlueBg;
          border: 1px solid $btnBlueBg;
        }
      }
    }
  }
}
