@import '../../../../@theme/index';

.sponsorsBenefit {
  width: 33%;
  @media (max-width: $lg) {
    width: 95%;
  }
  &__wrap {
    width: 90%;
    margin: auto;
    font-family: $roboto;
    @media (max-width: $sm-extra) {
      width: 100%;
    }
  }
  &__header {
    margin-bottom: 20px;
    @media (max-width: $lg) {
      margin-bottom: 0px;
      align-items: flex-start;
    }
  }
  &__image {
    width: 57px;
    height: 57px;
    object-fit: contain;
    @media (max-width: $xxl) {
      width: 50px;
      height: 50px;
    }
    @media (max-width: $xl) {
      width: 40px;
      height: 40px;
    }

    @media (max-width: $sm-mini) {
      width: 24px;
      height: 24px;
    }
    &__wrap {
      width: 105px;
      height: 105px;
      border-radius: 50%;
      background: $beASponsorGrayBg;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: $xxl) {
        width: 90px;
        height: 90px;
      }
      @media (max-width: $xl) {
        width: 75px;
        height: 75px;
      }
      @media (max-width: $xl) {
        width: 70px;
        height: 70px;
      }
      @media (max-width: $lg) {
        width: 70px;
        height: 70px;
      }
      @media (max-width: $sm-extra) {
        width: 60px;
        height: 60px;
      }
      @media (max-width: $sm-mini) {
        width: 40px;
        height: 40px;
      }
    }
  }
  &__title {
    font-weight: 500;
    font-size: 19px;
    width: 100%;
    text-align: center;
    color: $headerSummText;
    margin-top: 40px;
    @media (max-width: $lg) {
      width: 80%;
    }
    @media (max-width: $sm-mini) {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__text {
    font-size: 17px;
    font-weight: 300;
    color: $footerText;
    line-height: 23px;
    text-align: center;
    @media (max-width: $lg) {
      margin-left: 20%;
      position: relative;
      top: -45px;
    }
    @media (max-width: $sm-extra) {
      font-weight: normal;
      top: -25px;
    }
    @media (max-width: $sm-mini) {
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      position: relative;
      top: -15px;
    }
  }
}
