@import '../../../../@theme/index';

.swiftPayment {
  width: 55%;
  margin: 50px auto 40px;
  font-family: $roboto;
  text-align: center;

  &__title {
    font-weight: 500;
    font-size: 20px;
    color: $headerSummText;
    margin-bottom: 15px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: $darkGrayText;
  }
  &__backBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin: 30px auto 45px;
    cursor: pointer;
    &__img {
      margin-right: 10px;
    }
    &__text {
      font-size: 16px;
      line-height: 26px;
      color: $blueText;
      text-decoration: underline;
    }
  }
  &__boldText {
    font-weight: bold;
    color: #000000;
  }
  &__link {
    font-size: 16px;
    line-height: 26px;
    color: $blueText;
  }
  &__handlingPaymentText {
    margin-bottom: 40px;
  }
  &__transferInstructions {
    border: 1px solid $transferTableBorder;
    text-align: left;
    &__wrap {
      margin: 30px 30px 65px;
    }
    &__header {
      font-family: $roboto;
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: $blackText;
      padding-bottom: 35px;
      border-bottom: 1px solid $transferTableBorder;
    }
    &__table {
      display: flex;
      flex-wrap: wrap;
      &__row {
        width: 100%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-start;
        margin-top: 40px;
        &__firstColumn,
        &__secondColumn {
          width: 50%;
        }
      }
      &__key {
        font-family: $roboto;
        font-weight: 300;
        font-size: 15px;
        color: $grayText;
        line-height: 26px;
        margin-bottom: 15px;
      }
      &__value {
        font-family: $roboto;
        font-weight: 300;
        font-size: 17px;
        line-height: 26px;
        color: $blackText;
      }
    }
    &__infoAfterTable {
      margin-top: 40px;
      font-family: $roboto;
      font-weight: 400;
      font-size: 15px;
      color: $grayText;
      line-height: 28px;
      width: 75%;
    }
  }
  &__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    & button {
      margin: 0 10px;
    }
    &__helpLink {
      margin-left: 30px;
      text-decoration: underline;
      color: $blueText;
      font-family: $roboto;
      font-weight: 400;
      font-size: 14px;
    }
  }
}
