@import '../../../@theme/index';

.userMenu {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background-color: $userMenuBg;
  position: relative;
  font-size: 23px;
  margin-left: calc(100% - 55px);
  border: 4px solid transparent;
  transition: 0.4s;
  &.mini {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
  &:hover {
    border: 4px solid $btnLightGrayBg;
  }
  @media (max-width: $xl) {
    width: 40px;
    height: 40px;
    font-size: 18px;
  }
  &__name {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-transform: uppercase;
    color: $whiteText;
    text-decoration: none;
    font-family: $roboto;
    font-weight: 400;
    font-size: 23px;
    &.mini {
      font-size: 16px;
    }
  }
  &__submenu {
    transition: 0.5s;
    position: absolute;
    top: 140%;
    right: -15%;
    width: 370px;
    height: 360px;
    text-align: center;
    background-color: $whiteText;
    border: 1px solid $btnLightGrayBg;
    border-radius: 6px;
    font-family: $roboto;
    -webkit-box-shadow: 0px 0px 15px 0px rgba(197, 201, 204, 0.48);
    -moz-box-shadow: 0px 0px 15px 0px rgba(197, 201, 204, 0.48);
    box-shadow: 0px 0px 15px 0px rgba(197, 201, 204, 0.48);
    @media (max-width: $xl) {
      top: 175%;
    }
    @media (max-width: $sm) {
      top: 145%;
      width: 310px;
      height: 350px;
      right: 0;
    }
    @media (max-width: $sm-mini) {
      top: 145%;
      width: 260px;
      height: 365px;
    }
    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: 100%;
      right: 15px;
      border: 11px solid transparent;
      border-bottom-color: #dddddd;
      @media (max-width: $sm) {
        right: 5px;
      }
    }
    &::after {
      right: 16px;
      border: 10px solid transparent;
      border-bottom-color: #ffffff;
      @media (max-width: $sm) {
        right: 6px;
      }
    }
    &-wrap {
      padding: 30px 25px 20px;
    }
    &-circle {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      margin: auto;
      background: #fc5c65;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      font-weight: 400;
      font-size: 32px;
      margin-bottom: 20px;
    }
    &-name {
      font-weight: 500;
      font-size: 17px;
      color: $userMenuFullNameText;
      margin-bottom: 10px;
      @media (max-width: $sm) {
        font-size: 14px;
      }
    }
    &-mail {
      font-size: 15px;
      font-weight: 400;
      color: $sponsorText;
      margin-bottom: 15px;
    }
    &-settingsBtn button {
      border-radius: 30px;
      padding: 7px 25px 10px;
      background: $accountSettingsBtn;
      border: 1px solid $accountSettingsBtnBorder;
      font-family: $roboto;
      font-weight: 400;
      font-size: 14px;
      color: $accountSettingsBtnText;
      margin: 0 auto;
      &:hover {
        box-shadow: 0 1px 6px 0px #9a9a9aad;
      }
    }
    &-addAnotherAcc {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0px auto 15px;
      width: 100%;
      border: 1px solid $borderCountriesList;
      border-left: none;
      border-right: none;
      height: 50px;
      & img {
        width: 20px;
        height: 19px;
        margin-right: 10px;
      }
      & p {
        font-size: 16px;
        font-weight: 500;
        color: $grayText;
      }
    }
    &-signout button {
      padding: 10px 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: auto;
      box-sizing: border-box;
      background: $accountSettingsBtn;
      border: 1px solid $accountSettingsBtnBorder;
      border-radius: 24px;
      font-family: $roboto;
      font-weight: normal;
      font-size: 14px;
      color: $accountSettingsBtnText;
      &:hover {
        box-shadow: 0 1px 6px 0px #9a9a9aad;
      }
    }
  }
}

.user__sign-in {
  display: flex;
  justify-content: flex-end;
  & a {
    color: $signInText;
    font-size: 23px;
    font-family: $roboto;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    text-decoration: none;
    @media (max-width: $xxl) {
      font-size: 18px;
    }
    @media (max-width: $xl) {
      font-size: 18px;
    }
    @media (max-width: $lg) {
      font-size: 15px;
    }
    @media (max-width: $md) {
      font-size: 14px;
    }
  }
}
