@import '../../../@theme/index';

.accountSettings {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  border: 1px solid $borderHeaderPayment;
  box-sizing: border-box;
  border-top: none;
  @media (max-width: $lg) {
    flex-wrap: wrap;
  }
  &__sidebar {
    width: 27%;
    box-sizing: border-box;
    border-right: 1px solid $borderHeaderPayment;
    @media (max-width: $lg) {
      width: 100%;
    }
    &__userInfo {
      width: 100%;
      margin: 30px auto;
      &__image {
        display: flex;
        width: 75%;
        justify-content: center;
        margin: auto;
        &__nameCircle {
          width: 90px;
          height: 90px;
          border-radius: 50%;
          background: $userMenuBg;
          display: flex;
          justify-content: center;
          align-items: center;
          color: $whiteText;
          font-family: $roboto;
          font-weight: 400;
          font-size: 36px;
        }
      }
      &__userData {
        width: 100%;
        text-align: center;
        margin-top: 20px;
        font-family: $roboto;
        &__name,
        &__surname {
          font-weight: 500;
          line-height: 22px;
          font-size: 18px;
          color: $blackText;
        }
        &__email {
          color: $mainPageCategories;
          font-weight: 400;
          font-size: 16px;
          margin-top: 15px;
        }
      }
    }
    &__brandInfo {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      padding: 20px 30px;
      background: $beASponsorGrayBg;
      border-top: 1px solid $borderHeaderPayment;
      border-bottom: 1px solid $borderHeaderPayment;
      @media (max-width: $lg) {
        justify-content: center;
      }
      &__brandLogo {
        width: 50px;
        height: 50px;
        & img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      &__brandName {
        font-family: $roboto;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        color: $blackText;
        margin-left: 15px;
      }
    }
  }
  &__personalInfo {
    width: 100%;
    box-sizing: border-box;
  }
  &__sendInvite {
    width: 100%;
    margin: 35px 0;
    &__wrap {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    & button {
      margin: 0;
    }
  }
}
