@import '../../../@theme/index';

.userInfo {
  width: 100%;
  &__wrap {
    border-bottom: 1px solid $borderHeaderPayment;
    box-sizing: border-box;
    padding: 25px 30px;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    @media (max-width: $lg) {
      flex-wrap: wrap;
    }
  }
  &__image {
    width: 20px;
    height: 20px;
    & img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    margin-left: 20px;
    font-family: $roboto;
    font-weight: 500;
    font-size: 18px;
    color: $blackText;
    @media (max-width: $lg) {
      width: 90%;
    }
  }
  &__subTitle {
    font-size: 14px;
    font-weight: 400;
    color: $grayText;
    margin-top: 10px;
  }
  &__value {
    margin-left: auto;
    text-align: center;
    font-family: $roboto;
    font-weight: 400;
    font-size: 14px;
    color: $cancelBtnText;
    @media (max-width: $lg) {
      width: 100%;
      text-align: left;
      margin-top: 10px;
    }
  }
  &__verified {
    margin-top: 10px;
    font-family: $roboto;
    font-weight: 400;
    color: $grayText;
    display: flex;
    align-items: center;
    justify-content: center;
    &__icon {
      object-fit: contain;
      width: 15px;
      height: auto;
      margin-right: 5px;
    }
  }
  &__editButton {
    margin-left: auto;
    @media (max-width: $lg) {
      margin-left: 0;
      width: 100%;
    }
    & button {
      width: 90px;
      @media (max-width: $lg) {
        margin-top: 20px;
        width: 100%;
      }
    }
  }
  &__editBlock {
    opacity: 0;
    visibility: hidden;
    height: 0px;
    transition: all 0.0001s;
    transform: translate(0px, -50px);
    box-sizing: border-box;
    background: $submenuBg;
    &.show {
      height: 100%;
      opacity: 1;
      visibility: visible;
      transform: translate(0px, 0px);
      transition: all 0.5s, height 0.1s, opacity 1s;
    }
    &__wrap {
      padding: 30px 30px 30px 60px;
      box-sizing: border-box;
      border-bottom: 1px solid $borderHeaderPayment;
    }
  }
  &__billingAddress {
    & p {
      margin-top: 5px;
      text-align: left;
    }
  }
}
