@import '../../../@theme/index';

.radioButton:checked,
.radioButton:not(:checked) {
  position: absolute;
  left: -9999px;
}
.radioButton:checked + label,
.radioButton:not(:checked) + label {
  position: relative;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: black;
  font-size: 16px;
  width: 100%;
}
.radioButton:checked + label:before,
.radioButton:not(:checked) + label:before {
  content: '';
  position: absolute;
  right: 0;
  top: calc(50% - 8px);
  width: 16px;
  height: 16px;
  border: 1px solid $radioGrayBtn;
  border-radius: 100%;
  background: #fff;
}
.radioButton:checked + label:after,
.radioButton:not(:checked) + label:after {
  content: '';
  width: 10px;
  height: 10px;
  background: $blueText;
  position: absolute;
  top: calc(50% - 4px);
  right: 4px;
  border-radius: 100%;
  transition: all 0.2s ease;
}
.radioButton:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.radioButton:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}
