@import '../../../../@theme/index';

.editFullname {
  width: 100%;
  &__inputsField {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: $lg) {
      flex-wrap: wrap;
    }
  }
  &__label {
    display: block;
    font-family: $roboto;
    font-size: 16px;
    font-weight: 500;
    color: $darkestGrayText;
    margin-bottom: 10px;
    &__required {
      color: $requiredFormFieldText;
    }
    &__optional {
      color: $grayText;
    }
  }
  &__input {
    width: 31%;
    @media (max-width: $lg) {
      width: 100%;
      margin: 15px 0;
    }
    & input {
      width: 100%;
      height: 40px;
      padding: 15px 20px;
      box-sizing: border-box;
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      color: $blackText;
      border: 1px solid $btnLightGrayBg;
      border-radius: 4px;
      &:focus {
        outline: none;
        border-color: $blueText;
      }
    }
  }
  &__note {
    margin-top: 30px;
    width: 90%;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: $grayText;
  }
  &__boldText {
    font-family: $roboto;
    font-weight: 500;
    color: $cancelBtnText;
  }
  &__link {
    font-family: $roboto;
    text-decoration: none;
    color: $blueText;
  }
  &__addMore {
    margin: 20px 0;
    padding: 20px 0;
    border-bottom: 1px solid $btnLightGrayBg;
    border-top: 1px solid $btnLightGrayBg;
    box-sizing: border-box;
    font-family: $roboto;
    &__boldText {
      font-size: 16px;
      font-weight: 500;
      margin-right: 20px;
    }
    &__link {
      font-weight: 400;
      font-size: 15px;
      text-decoration: none;
      color: $blueText;
    }
  }
  &__buttons {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    &__btn {
      font-family: $roboto;
      font-size: 14px;
      font-weight: 500;
      border: 1px solid transparent;
      height: 30px;
      box-sizing: border-box;
      &:disabled {
        background: $btnDisabledBg;
        color: $mainPageCategories;
      }
      &__cancel {
        color: $darkGrayText;
        background: $btnLightGrayBg;
        border: 1px solid $borderSignOutBtn;
        margin-right: 20px;
        width: 90px;
      }
      &__save {
        color: $whiteText;
        background: $btnBlueBg;
        width: 125px;
      }
    }
    &__cancel {
      margin-right: 20px;
    }
  }
}
