@import '../../../@theme/index';

.fullScreenLayout {
  width: 100%;
  transition: 0.5s;
}
.with-shadow {
  box-shadow: 0 15px 25px 0px #d6d6d6;
  position: relative;
}

.fixed {
  position: fixed;
  z-index: 25;
  top: 0;
  border-bottom: 1px solid #ebebeb;
  height: inherit;
  @media (max-width: $sm-extra) {
    box-sizing: border-box;
  }
}

.payment__absolute {
  position: fixed;
  top: 80px;
  z-index: 15;
  -webkit-box-shadow: -2px 8px 18px 0px rgba(0, 0, 0, 0.13);
  box-shadow: -2px 8px 18px 0px rgba(0, 0, 0, 0.13);
}
