@import '../../@theme/index';

.authLayout {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 80vh;
  @media (max-width: $sm-extra) {
    height: auto;
    display: block;
  }
}
.auth {
  position: relative;
  display: flex;
  justify-content: space-between;
  border: 1px solid $btnLightGrayBg;
  border-radius: 5px;
  box-shadow: 10px 5px 25px 0px #8080804f;
  height: 490px;
  width: 850px;
  box-sizing: border-box;
  margin: auto;

  @media (max-width: $lg) {
    max-width: 697px;
  }
  @media (max-width: $sm-extra) {
    flex-wrap: wrap;
    width: 100%;
    box-shadow: none;
    border: none;
  }
  &__search {
    &-image-wrapper {
      overflow: hidden;
      max-width: 320px;
      width: 100%;
      height: 100%;
      @media (max-width: $sm-extra) {
        max-width: none;
        width: 100%;
        height: 187px;
      }
    }
    &-image {
      height: 100%;
      position: relative;
      transition: 2s;

      &-circle {
        position: absolute;
        max-width: 172px;
        max-height: 172px;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        border-radius: 50%;
        border: 3px solid #ffffff;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        transition: 2s;
        @media (max-width: $lg) {
          top: 47%;
          left: 50%;
          max-width: 145px;
          max-height: 145px;
        }
        @media (max-width: $sm-extra) {
          width: 126px;
          height: 126px;
        }
      }
    }
  }
  &__sign-up {
    margin: 0 55px;
    width: 100%;
    @media (max-width: $sm-extra) {
      margin: 10% auto;
      width: 95%;
      min-height: 222px;
    }
    &-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 85px;
      @media (max-width: $sm-extra) {
        margin-top: 0;
      }
    }
    &-logo-wrapper {
      margin-top: 15px;
      @media (max-width: $sm-extra) {
        display: none;
      }
    }
    &-title {
      margin: 30px auto 35px;
      font-family: $roboto;
      font-weight: 300;
      font-size: 30px;
      position: relative;
      top: -7px;
      color: $userMenuFullNameText;
      @media (max-width: $sm-extra) {
        margin: 10px auto 35px;
        font-size: 22px;
        font-style: normal;
        font-weight: normal;
      }
    }
    &-forgot-password {
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      position: absolute;
      top: calc(100% + 20px);
      right: 0;
      color: $blueText;
      text-decoration: none;
      display: block;
      &:focus {
        border: 1px solid $blackText;
      }
      @media (max-width: $sm-extra) {
        left: 18px;
        text-align: left;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
      }
    }
    &-label {
      width: 100%;
      position: relative;
      margin-bottom: 10px;
      display: flex;
      align-items: flex-end;

      & label {
        font-family: $roboto;
        font-size: 16px;
        font-weight: 500;
        color: $darkestGrayText;
      }
      &-validationIcons {
        width: 20px;
        height: 20px;
        margin-left: 5px;
        opacity: 0;
        visibility: hidden;
        transition: 0.4s;
        &__show {
          opacity: 1;
          visibility: visible;
        }
        & img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
    &-input-wrapper {
      width: 100%;
      position: relative;
    }
    &-input {
      border: 1px solid $loginBorder;
      border-radius: 2px;
      padding: 10px 18px;
      font-size: 18px;
      width: 100%;
      box-sizing: border-box;
      font-family: $roboto;
      font-weight: 400;
      -webkit-appearance: none;
      &::placeholder {
        color: $borderMinusBtn;
        font-size: 18px;
        letter-spacing: 0px;
      }
      &:focus {
        outline: none;
      }
      &[type='password'] {
        letter-spacing: 3px;
      }
      @media (max-width: $sm-extra) {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 20px;
      }
      &-clearEmail {
        position: absolute;
        background: url(../../assets/images/ui-icons/closeBurgerMenu.svg)
          no-repeat;
        background-size: contain;
        background-position: center;
        transform: translate(-50%, -50%);
        top: 50%;
        right: 2%;
        width: 14px;
        height: 14px;
      }
      &-hide-password {
        position: absolute;
        background: url(../../assets/images/ui-icons/passwordGrayEye.png)
          no-repeat;
        background-size: contain;
        background-position: center;
        transform: translate(-50%, -50%);
        top: 50%;
        right: 2%;
        width: 25px;
        height: 25px;
        @media (max-width: $sm-extra) {
          width: 20px;
          height: 20px;
        }
      }
      &-show-password {
        position: absolute;
        background: url(../../assets/images/ui-icons/passwordBlackEye.png)
          no-repeat;
        background-size: contain;
        background-position: center;
        transform: translate(-50%, -50%);
        top: 50%;
        right: 2%;
        width: 25px;
        height: 25px;
        @media (max-width: $sm-extra) {
          width: 20px;
          height: 20px;
        }
      }
    }
    &-button-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      &.email {
        margin-top: 31px;
      }
      &.password {
        margin-top: 62px;
      }
    }
    &-passwordWrapper {
      margin-top: 31px;
    }
  }
}

.errorText {
  position: absolute;
  top: 110%;
  left: 0;
  font-family: $roboto;
  font-weight: 400;
  font-size: 14px;
  color: $inputErrorText;
  @media (max-width: $sm-mini) {
    font-size: 12px;
  }
}

.error {
  border: 1px solid $inputErrorBorder;
}

.success {
  border: 1px solid $inputSuccessBorder;
}

.single__image {
  position: absolute;
  opacity: 0;
  transition: 1s;
  top: 50%;
  left: 19%;
  transform: translate(-50%, -50%);
  z-index: 11;
  width: 170px;
  height: 170px;
  object-fit: contain;
  @media (max-width: $lg) {
    width: 145px;
    height: 145px;
  }
  @media (max-width: $sm-extra) {
    display: none;
  }
  & img {
    display: block;
    margin: auto;
  }
  &.show-image {
    opacity: 1;
    transition: 1s;
  }
  &.first-image {
    animation: first-image 55s infinite;
  }
  &.second-image {
    animation: second-image 55s infinite;
    // max-height: 150px;
  }
  &.third-image {
    animation: third-image 55s infinite;
    // max-height: 100px;
  }
  &.fourth-image {
    animation: fourth-image 55s infinite;
    // max-height: 150px;
  }
  &.fifth-image {
    animation: fifth-image 55s infinite;
    // max-height: 120px;
  }
  &.six-image {
    animation: sixth-image 55s infinite;
    // max-height: 120px;
  }
  &.seventh-image {
    animation: seventh-image 55s infinite;
    // max-height: 110px;
  }
  &.eight-image {
    animation: eight-image 55s infinite;
    // max-height: 120px;
  }
  &.nineth-image {
    animation: ninent-image 55s infinite;
    // max-height: 120px;
  }
  &.ten-image {
    animation: ten-image 55s infinite;
    // max-height: 120px;
  }
  &.eleven-image {
    animation: eleven-image 55s infinite;
    // max-height: 90px;
  }
}
