@import '../../../@theme/index';

.checkbox__input {
  display: none;
}

.checkbox {
  position: relative;
  display: inline-flex;
  cursor: pointer;
}

.checkbox__text {
  font-family: $roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $blackText;
  &__grayText {
    color: $sponsorText;
  }

  &::before {
    display: inline-block;
    content: '';
    position: relative;
    top: 3px;
    margin-right: 8px;
    width: 18px;
    height: 18px;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    box-sizing: border-box;
    @media (max-width: $sm-extra) {
      width: 18px;
      height: 18px;
    }
  }
  @media (max-width: $sm-extra) {
    font-size: 14px;
    line-height: 20px;
  }
}

.checkbox__input:checked + .checkbox__text::before {
  background: $blueText;
}

.checkbox__input {
  &__green {
    &:checked + .checkbox__text::before {
      background: $inputSuccessBorder;
      border: none;
    }
  }
}
.checkbox__input:checked + .checkbox__text::after,
.checkbox__input_checked + .checkbox__text::after {
  position: absolute;
  content: '';
  left: 3px;
  top: 8px;
  width: 11px;
  height: 8px;
  background: url(../../../assets/images/ui-icons/whiteTick.png) no-repeat;
  background-size: 11px 8px;
}
