@import '../../@theme/index';

.myAccount {
  width: 70%;
  margin: 20px auto 0;
  &__tabMenu {
    width: 100%;
    font-family: $roboto;
    font-weight: 400;
    height: 75px;
    background: $beASponsorGrayBg;
    border: 1px solid $borderHeaderPayment;
    box-sizing: border-box;

    &__list {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 100%;
      margin-left: 30px;
    }
    &__menuItem {
      height: 100%;

      &__link {
        height: 100%;
        display: flex;
        align-items: center;
        text-decoration: none;
        color: $grayText;
        font-size: 17px;
        height: 100%;
        box-sizing: border-box;
        padding: 25px 20px;
        border: 1px solid transparent;
        @media (max-width: $lg) {
          text-align: center;
        }
        &.active {
          color: $blueText;
          background: $whiteBg;
          border-left: 1px solid $borderHeaderPayment;
          border-right: 1px solid $borderHeaderPayment;
          box-sizing: border-box;
          position: relative;
          &::before {
            content: '';
            width: 100%;
            position: absolute;
            height: 2px;
            top: -2px;
            left: 0;
            background: $blueText;
          }
          &::after {
            content: '';
            width: 100%;
            position: absolute;
            height: 3px;
            bottom: -3px;
            left: 0;
            background: $whiteBg;
          }
        }
      }
    }
  }
  &__content {
    width: 100%;
    background: #ffffff;
  }
}
