@import '../../../@theme/index';

.pay-check {
  width: 75%;
  margin: auto;
  background: $bgGray;
  border: 1px solid $borderHeaderPayment;
  border-bottom: none;
  transition: 0.2s;
  @media (max-width: $sm-extra) {
    width: 100%;
    background: $bgMain;
    border: none;
    border-top: 1px solid $borderHeaderPayment;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: none;
    height: 70px;
    padding: 0 30px;
    &.wrapToContent {
      width: 70%;
      margin: auto;
    }
    @media (max-width: $md-extra) {
      flex-wrap: wrap;
    }
    @media (max-width: $sm-extra) {
      width: 70%;
      min-height: 82px;
      margin: auto;
      padding: 24px 0;
    }
  }
  &.fullWidth {
    border-top: none;
    width: 100%;
  }

  &__title {
    font-family: $roboto;
    width: 35%;
    font-weight: 400;
    font-size: 15px;
    color: $darkGrayText;
    @media (max-width: $md-extra) {
      display: none;
    }
  }
  &__result {
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    @media (max-width: $xl) {
      width: 55%;
    }
    @media (max-width: $lg) {
    }
    @media (max-width: $md-extra) {
      width: 100%;
    }
    @media (max-width: $sm-extra) {
      flex-wrap: wrap;
      width: 100%;
    }
  }
  &__right-title {
    font-family: $roboto;
    font-weight: 300;
    color: $grayText;
    font-size: 18px;
    width: 35%;
    text-align: center;
    @media (max-width: $xl) {
      font-size: 16px;
    }
    @media (max-width: $lg) {
      width: 30%;
      font-size: 15px;
    }
    @media (max-width: $sm-extra) {
      width: 60%;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__summ {
    font-family: $roboto;
    font-weight: 400;
    font-size: 30px;
    color: $headerSummText;
    margin: 0 auto;
    width: 40%;
    text-align: center;
    @media (max-width: $xl) {
      font-size: 23px;
      margin: 5% auto;
    }
    @media (max-width: $md-extra) {
      width: 35%;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
    }
    @media (max-width: $sm-extra) {
      margin: 0;
    }
  }
  &__button {
    @media (max-width: $sm-extra) {
      width: 100%;
      text-align: center;
      margin-top: 18px;
    }
    & button {
      border-radius: 24px;
      @media (max-width: $sm-extra) {
        width: 160px;
        height: 44px;
      }
    }
  }
}
