@import '../../../../@theme/index';

.members {
  width: 100%;
  box-sizing: border-box;
  &__wrap {
    margin: 30px 35px;
  }
  &__title {
    font-family: $roboto;
    font-weight: 400;
    font-size: 24px;
    color: $blueText;
    margin-bottom: 30px;
  }
  &__list {
    &__header {
      display: flex;
      align-items: center;
      background: $blueText;
      padding: 15px 30px 15px 15px;
      box-sizing: border-box;
      &__checkbox {
        &__wrap {
          margin-left: 35px;
        }
      }

      &__selectedCount {
        font-family: $roboto;
        font-size: 14px;
        color: $whiteText;
        font-weight: 500;
        margin: 0 30px 0 10px;
        position: relative;
      }
      &__selectBar {
        width: 155px;
        margin-left: 20px;
        position: relative;
        &__borderLine {
          position: absolute;
          top: 0;
          height: 100%;
          width: 1px;
          background: white;
          &.left {
            left: -27.5px;
          }
          &.right {
            right: -27.5px;
          }
        }
      }
      &__close {
        height: 38px;
        margin-left: auto;
        position: relative;
        display: flex;
        align-items: center;
        cursor: pointer;
      }
    }
    &__body {
      width: 100%;
      border-bottom: 1px solid $borderHeaderPayment;

      &__wrap {
        margin: 0px 35px 0;
      }
    }
  }
  &__pagination {
    width: 100%;
    background: $accountSettingsMemberHover;
    &__wrap {
      width: 70%;
      margin: auto;
      padding: 20px 0;
    }
    &__controllers {
      display: flex;
      justify-content: center;
      &__button {
        margin: 0 10px;
        &__btn {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          background: $whiteBg;
          border: 1px solid $borderHeaderPayment;
          box-sizing: border-box;
          display: flex;
          justify-content: center;
          align-items: center;
          &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
          }
          &.next {
            transform: rotate(180deg);
          }
        }
      }
      &__pages {
        display: flex;
        align-items: center;
        background: $whiteBg;
        border-radius: 30px;
        box-sizing: border-box;
        border: 1px solid $borderHeaderPayment;
        font-family: $roboto;
        font-weight: 500;
        font-size: 16px;
        color: $mainPageCategories;
        &__item {
          cursor: pointer;
          width: 35px;
          border-radius: 50%;
          background: $whiteBg;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          text-decoration: none;
          color: $mainPageCategories;
          &.active {
            background: $blueText;
            color: $whiteText;
          }
          // &:last-child {
          //   padding-right: 10px;
          // }
        }
      }
    }
  }
}
