@import "../../@theme/index";

.payment {
  display: flex;
  justify-content: flex-end;
  padding: 2%;
  font-family: $roboto;
  background: $bgGray;
  border: 1px solid #ededed;
  border-top: none;
  @media (max-width: $sm-extra) {
    margin-top: 32px;
    background: #fff;
    border: none;
  }
  &__window {
    width: 40%;
    @media (max-width: $xxlMiddle) {
      width: 45%;
    }
    @media (max-width: $xxl) {
      width: 50%;
    }
    @media (max-width: $lg) {
      width: 55%;
    }
    @media (max-width: $md) {
      width: 60%;
    }
    @media (max-width: $sm-extra) {
      width: 100%;
    }
    &-promo-text {
      font-size: 15px;
      font-weight: 400;
      color: $blackText;
      @media (max-width: $xl) {
        font-size: 12px;
      }
      @media (max-width: $sm-extra) {
        color: $userMenuFullNameText;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        width: 50%;
        line-height: 20px;
      }
    }
    &-total-pledged {
      font-size: 18px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 20px;
      border-bottom: 1px solid $borderHeaderPayment;
      margin: 5% auto;
      @media (max-width: $sm-extra) {
        margin: 13px auto 20px;
        padding-bottom: 10px;
      }
      &-title {
        color: $grayText;
        font-weight: 300;
        font-size: 18px;
        @media (max-width: $xl) {
          font-size: 15px;
        }
        @media (max-width: $lg) {
          font-size: 14px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
        }
      }
      &-price {
        font-weight: 400;
        color: $blackText;
        @media (max-width: $xl) {
          font-size: 14px;
        }
        @media (max-width: $lg) {
          font-size: 13px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 24px;
        }
      }
    }
    &-total-price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 5% auto;
      @media (max-width: $xl) {
        margin: 0 auto 27px;
      }
      &-title {
        font-size: 20px;
        color: $darkestGrayText;
        font-weight: 300;
        @media (max-width: $xl) {
          font-size: 16px;
        }
        @media (max-width: $lg) {
          font-size: 14px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 24px;
        }
      }
      &-final-price {
        font-size: 30px;
        color: $blackText;
        font-weight: 400;
        @media (max-width: $xl) {
          font-size: 18px;
        }
        @media (max-width: $lg) {
          font-size: 16px;
        }
        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: 500;
          font-size: 22px;
          line-height: 30px;
        }
      }
    }
    &-buttons {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      @media (max-width: $xl) {
        flex-wrap: wrap;
        flex-direction: column;
        margin-bottom: 25px;
      }
      & button {
        width: 48%;
        margin: auto 1%;
        @media (max-width: $xl) {
          margin: 2% auto;
          font-size: 15px;
        }
        @media (max-width: $lg) {
          font-size: 12px;
        }
        @media (max-width: $md-extra) {
          font-size: 13px;
        }
        @media (max-width: $sm-extra) {
          width: 260px;
        }
      }
      & button:first-child {
        font-size: 15px;
        color: $accountSettingsBtnText;
        background: $btnMinusHoverGb;

        @media (max-width: $md-extra) {
          font-size: 13px;
        }

        @media (max-width: $sm-extra) {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
  }
}
