@import '../../../../../@theme/index';

.singleMember {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 30px 20px 15px;
  background: $whiteBg;
  border-bottom: 1px solid $borderHeaderPayment;
  &:last-child {
    border-bottom: none;
  }
  &:hover > &__settings {
    opacity: 1;
    visibility: visible;
  }
  &:hover {
    background: $accountSettingsMemberHover;
  }
  &__fullname {
    font-family: $roboto;
    font-weight: 400;
    font-size: 14px;
    color: $blackText;
  }
  &__department {
    font-family: $roboto;
    font-size: 14px;
    font-weight: 500;
    color: $squareBenefitText;
    margin-left: auto;
  }
  &__lastOnlineDate {
    margin-right: 35px;
    margin-left: 70px;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 500;
    color: $accountSettingsMembersLastOnlineDate;
  }
  &__registrationDate {
    font-family: $roboto;
    font-size: 14px;
    font-weight: 500;
    color: $squareBenefitText;
    margin-right: 15px;
  }
  &__settings {
    opacity: 0;
    visibility: hidden;
    position: relative;
    transition: 0.1s;
    &__optionsBar {
      position: absolute;
      top: calc(100% + 20px); //padding of .singleMember = 20px, and + 10%
      opacity: 0;
      visibility: hidden;
      transition: 0.2s;
      background: $accountSettingsMemberHover;
      cursor: pointer;
      width: max-content;
      padding: 10px 30px 10px 10px;
      right: -30px;
      box-sizing: border-box;
      border: 1px solid $borderHeaderPayment;
      font-family: $roboto;
      font-size: 13px;
      font-weight: 400;
      color: $blackText;
      &::before {
        content: '';
        display: block;
        border: 12px solid transparent;
        border-bottom-color: #ddd;
        position: absolute;
        top: -25px;
        right: 25px;
      }
      &::after {
        content: '';
        display: block;
        border: 12px solid transparent;
        border-bottom-color: $accountSettingsMemberHover;
        position: absolute;
        top: -24px;
        right: 25px;
      }
      &.show {
        opacity: 1;
        visibility: visible;
      }
    }
    &__icon {
      cursor: pointer;
      padding: 5px;
      box-sizing: border-box;
    }
  }
}
