@import '../../@theme/index';

.mainPage {
  &__header {
    width: 85%;
    margin-left: 15%;
    background: url(../../assets/images/beASponsorHeader.jpg);
    background-repeat: no-repeat;
    background-position: right top;
    @media (max-width: $xxlMiddle) {
      background-position-x: 250px;
    }
    @media (max-width: $xl) {
      width: 100%;
      margin-left: 0%;
      background-size: 75%;
      background: url(../../assets/images/mainPageMobileHeaderBG.png);
      background-repeat: no-repeat;
      background-position: top right;
      background-size: contain;
    }

    &_wrap {
      width: 85%;
      padding: 7% 0 5% 0;
      @media (max-width: $xl) {
        margin: auto;
      }
      @media (max-width: $md) {
        width: 90%;
      }
    }
    &__box {
      display: flex;
      justify-content: flex-start;
      @media (max-width: $md) {
        flex-wrap: wrap;
      }
    }
    &__text {
      width: 40%;
      @media (max-width: $md) {
        width: 100%;
      }
    }
    &__title {
      font-family: $bitter;
      font-weight: bold;
      font-size: 40px;
      line-height: 52px;
      color: $darkGrayText;
      text-align: left;
      & span {
        color: $becomeSponsorGreenText;
      }
      @media (max-width: $xl) {
        font-size: 32px;
        line-height: 42px;
      }
      @media (max-width: $sm) {
        font-size: 22px;
        line-height: 26px;
      }
      @media (max-width: $sm-mini) {
        font-size: 18px;
        width: 95%;
      }
    }
    &__subtitle {
      font-family: $roboto;
      font-weight: 400;
      color: $sponsorText;
      font-size: 24px;
      margin-top: 45px;
      width: 90%;
      line-height: 32px;
      @media (max-width: $xl) {
        font-size: 20px;
      }
      @media (max-width: $md) {
        margin-top: 12px;
        line-height: 26px;
        width: 75%;
      }
      @media (max-width: $sm-mini) {
        line-height: 20px;
        font-size: 14px;
      }
    }
    &__buttons {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 80%;
      margin-top: 50px;
      @media (max-width: $md-extra) {
        flex-wrap: wrap;
      }
      @media (max-width: $md) {
        width: 48%;
      }
      @media (max-width: $sm-extra) {
        width: 50%;
      }

      & a:first-child {
        background: $blueText;
        color: white;
        @media (max-width: $xl) {
          margin: 5% 0;
        }
        @media (max-width: $md-extra) {
          width: 166px;
          height: 42px;
          margin: 5% 0;
        }
      }
      & a:nth-child(2) {
        background: $becomeSponsorGreenText;
        color: white;
        width: 220px;
        margin-left: 30px;
        @media (max-width: $xl) {
          // margin: 3% 0;
        }
        @media (max-width: $md-extra) {
          width: 166px;
          height: 42px;
          margin: 3% 0;
        }
      }
    }
    &__videoBlock {
      margin-left: 10%;
      width: 50%;
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;
      @media (max-width: $xxl) {
        margin-left: 5%;
        width: 55%;
        align-items: center;
      }
      @media (max-width: $md) {
        opacity: 0;
        visibility: hidden;
      }
      &__wrap {
        width: 75%;
        height: 100%;
        position: relative;
        transition: 0.5s;
        transform: scale(1);

        @media (max-width: $xxl) {
          width: 100%;
          height: 75%;
        }
      }
      &__videoWrap {
        height: 95%;
        width: 100%;
        position: relative;
      }
      &__controllers {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        &__stopVideoBtn {
          transition: 0.5s;
          opacity: 0;
          visibility: hidden;
        }
        & button {
          background: none;
          border: none;
          width: 72px;
          height: 72px;
          transition: 0.5s;
          &:hover {
            transform: scale(1.1);
          }
          & img {
            max-width: 100%;
            border-radius: 50%;
          }
        }
      }
      &__videoMenu {
        height: 5%;
        width: 100%;
        background: $blueText;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }

      &__full {
        margin-right: 10px;
        display: block;
      }
      & video {
        width: 100%;
        height: 100%;
        -webkit-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
        box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
        object-fit: cover;
      }
    }
  }

  &__benefits {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: auto;
    border: 1px solid $btnLightGrayBg;
    border-bottom: none;
    font-family: $roboto;
    font-weight: 400;
    color: $headerSummText;
    @media (max-width: $xl) {
      flex-wrap: wrap;
      border: none;
      margin-bottom: 50px;
    }
  }

  &__description {
    position: relative;
    &__wrap {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      border-top: 1px solid $btnLightGrayBg;
    }
    &__box {
      width: 50%;
      @media (max-width: $md) {
        width: 100%;
      }
      &__wrap {
        margin-top: 5%;
        margin-bottom: 4%;
        transition: 0.7s;
        &.firstWrap {
          transform: translate(-30px, 0);
          opacity: 0;
          visibility: hidden;
          z-index: 1;
          &.show {
            transform: translate(0, 0);
            opacity: 1;
            visibility: visible;
          }
        }
        &.secondWrap {
          width: 50%;
          margin-left: 20%;
          @media (max-width: $xxl) {
            width: 65%;
          }
          @media (max-width: $xl) {
            width: 70%;
          }
          @media (max-width: $lg) {
            width: 80%;
          }
          @media (max-width: $md) {
            width: 90%;
            margin: 5% auto;
          }
        }
      }
      &_firstBlock {
        background: url(../../assets/images/beASponsorDescriptionBlockLeftImageBG.png)
          no-repeat;
        background-position: center;
        background-size: cover;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        &::before {
          content: '';
          width: 40%;
          background: #4285f4;
          height: 1px;
          display: block;
          position: absolute;
          bottom: 20%;
          right: -15%;
          z-index: 3;
          @media (max-width: $md) {
            width: 13%;
            right: 40%;
            bottom: 22%;
          }
        }
        &::after {
          content: '';
          width: 5px;
          border-radius: 50%;
          background: #4285f4;
          height: 5px;
          position: absolute;
          bottom: calc(20% - 2px);
          right: -15%;
          z-index: 3;
          @media (max-width: $md) {
            width: 5px;
            right: 40%;
            bottom: calc(22% - 2px);
          }
        }
        @media (max-width: $sm-extra) {
          order: 2;
        }
        &__image {
          object-fit: contain;
          position: relative;
          right: -5%;
          width: 100%;
          height: 100%;
          @media (max-width: $md) {
            right: 0;
            width: 70%;
          }
        }
        &__underDescription {
          position: absolute;
          right: -90%;
          bottom: 12.5%;
          z-index: 3;
          width: 65%;
          font-family: $roboto;
          font-weight: 500;
          font-size: 20px;
          color: $blackText;
          transform: translate(200px, 0);
          opacity: 0;
          visibility: hidden;
          transition: 1s;
          @media (max-width: $xxlMiddle) {
            right: -85%;
          }
          @media (max-width: $xl) {
            bottom: 10%;
            font-size: 18px;
          }
          @media (max-width: $md-extra) {
            font-size: 14px;
            width: 75%;
            right: -95%;
          }
          @media (max-width: $md) {
            right: 0;
            bottom: 12.5%;
            width: 35%;
          }
          @media (max-width: $sm-extra) {
            font-size: 12px;
          }
        }
      }
      &__title {
        font-family: $bitter;
        font-weight: bold;
        font-size: 40px;
        line-height: 52px;
        color: $darkGrayText;
        transform: translate(0, 40px);
        opacity: 0;
        visibility: hidden;
        transition: 0.4s;
        @media (max-width: $xxl) {
          font-size: 34px;
          line-height: 48px;
        }
        @media (max-width: $xl) {
          font-size: 32px;
          line-height: 42px;
        }
        @media (max-width: $md-extra) {
          font-size: 28px;
          line-height: 32px;
          width: 100%;
        }
        @media (max-width: $md) {
          width: 75%;
        }
        @media (max-width: $sm-extra) {
          font-size: 26px;
          line-height: 32px;
        }
        @media (max-width: $sm-mini) {
          font-size: 18px;
          line-height: 26px;
        }
      }
      &_secondBlock {
        background: $beASponsorGrayBg;
        position: relative;
        @media (max-width: $sm-extra) {
          order: 1;
        }
        &__description {
          font-family: $roboto;
          font-weight: 400;
          font-size: 24px;
          line-height: 32px;
          color: $lightGrayText;
          margin-top: 50px;
          margin-left: 0;
          transform: translate(0, 100px);
          opacity: 0;
          visibility: hidden;
          transition: 0.7s;
          @media (max-width: $xxl) {
            font-size: 21px;
            line-height: 28px;
            margin-top: 40px;
          }
          @media (max-width: $xl) {
            margin-top: 30px;
            font-size: 19px;
            line-height: 1;
          }
          @media (max-width: $lg) {
            margin-top: 15px;
            font-size: 18px;
            line-height: 1;
          }
          @media (max-width: $md) {
            margin-top: 30px;
            width: 75%;
            font-size: 21px;
            line-height: 26px;
          }
          @media (max-width: $sm-mini) {
            line-height: 16px;
            font-size: 12px;
            margin-top: 10px;
          }
        }
      }
      &_thirdBlock {
        background: $beASponsorThirdBlockBg;
        @media (max-width: $sm-extra) {
          background: $whiteText;
          order: 3;
        }
        &__wrap {
          margin: 10% 10% 13% 25%;
          @media (max-width: $md) {
            width: 90%;
            margin: 5% auto;
          }
        }
        &__title {
          width: 90%;
        }
        &__description {
          margin-top: 35px;
          font-family: $roboto;
          font-size: 24px;
          line-height: 32px;
          color: $beASponsorThirdBlockDescriptionText;
          transform: translate(200px, 0);
          opacity: 0;
          visibility: hidden;
          transition: 1.2s;
          @media (max-width: $md) {
            font-size: 21px;
            line-height: 26px;
            margin-top: 30px;
          }
          @media (max-width: $sm-mini) {
            font-size: 12px;
            line-height: 16px;
            margin-top: 10px;
          }
        }
      }
      &_fourthBlock {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        @media (max-width: $sm-extra) {
          flex-direction: row;
          z-index: 3;
          order: 4;
        }
        &__wrap {
          height: 85%;
          width: 60%;
          margin-left: 5%;
          transform: translate(0, 200px);
          opacity: 0;
          visibility: hidden;
          transition: 1.2s;
          @media (max-width: $md) {
            height: 90%;
            width: 80%;
            margin-left: 10%;
            display: flex;
          }

          & img {
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
      }
    }
    &__lastImage {
      position: absolute;
      left: 50%;
      bottom: -10%;
      z-index: 2;
      transform: translate(-400px, 0);
      opacity: 0;
      visibility: hidden;
      transition: 1.4s;
      &.show {
        transform: translate(-50%, 0);
      }
      @media (max-width: $md) {
        bottom: -5%;
        left: 35%;
      }
      & img {
        max-width: 550px;
        -webkit-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
        box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
      }
    }
  }

  &__whyWeBlock {
    width: 75%;
    margin: 7% auto 0%;
    transform: translate(0, -30px);
    opacity: 0;
    visibility: hidden;
    transition: 1s;
    overflow: hidden;
    @media (max-width: $md) {
      width: 90%;
    }
  }

  &__exploreSponsorhip {
    background: url(../../assets/images/fifthBlockBg.jpg) no-repeat;
    background-size: 100% 100%;
    width: 100%;
    overflow: hidden;

    @media (max-width: $sm-mini) {
      background: url(../../assets/images/exploreSponsorshipBG.png);
    }
    &__wrap {
      padding-top: 70px;
      padding-bottom: 45px;
      @media (max-width: $sm-mini) {
        padding-top: 20px;
      }
    }
    &__textBlock {
      width: 50%;
      margin: auto;
      @media (max-width: $md) {
        width: 90%;
      }
    }
    &__title {
      font-family: $bitter;
      font-size: 40px;
      font-weight: bold;
      color: $darkGrayText;
      text-align: center;
      width: 75%;
      margin: auto;
      line-height: 1.2;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 100px);
      transition: 0.6s;

      @media (max-width: $sm-mini) {
        font-size: 18px;
        line-height: 26px;
        width: 100%;
        text-align: left;
      }
    }
    &__description {
      font-family: $roboto;
      font-weight: 300;
      font-size: 23px;
      line-height: 32px;
      color: $darkGrayText;
      text-align: center;
      margin-top: 30px;
      opacity: 0;
      visibility: hidden;
      transform: translate(0px, 300px);
      transition: 1.5s;

      @media (max-width: $sm-mini) {
        margin-top: 10px;
        font-size: 14px;
        line-height: 20px;
        width: 90%;
        text-align: left;
        color: #828282;
        font-weight: normal;
      }
    }
    &__btnWrap {
      margin-top: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 500px);
      transition: 1.7s;

      @media (max-width: $sm-mini) {
        margin-top: 30px;
        justify-content: flex-start;
      }
    }
    &__sponsorImageWrap {
      width: 65%;
      margin: 50px auto;
      position: relative;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 700px);
      transition: 2s;

      @media (max-width: $sm-extra) {
        width: 90%;
        margin: 20px auto 0;
        height: 25vh;
      }
      & img {
        margin: auto;
        display: block;
        object-fit: contain;
        @media (max-width: $sm-extra) {
          width: 100%;
          height: 100%;
        }
      }
      &__firstImage {
        max-width: 895px;
        display: flex;
      }
      &__secondImage {
        position: absolute;
        max-width: 430px;
        right: -30%;
        transform: translate(-50%, -50%);
        top: 50%;
      }
      &__thirdImage {
        position: absolute;
        max-width: 260px;
        top: 20%;
        left: 0%;
      }
    }
  }

  &__parthnerMaterial {
    background: url(../../assets/images/blueBlockBG.jpg) no-repeat;
    width: 100%;
    height: 840px;
    background-size: cover;
    display: flex;
    overflow: hidden;
    @media (max-width: $xl) {
      background-position: bottom;
    }
    @media (max-width: $md) {
      background: #2f80ed;
    }
    @media (max-width: $sm-extra) {
      height: 600px;
    }
    &__wrap {
      width: 70%;
      // height: 85%;
      margin: 2.5% auto;
      @media (max-width: $xl) {
        height: 85%;
      }
      @media (max-width: $md) {
        margin: 5% auto;
        width: 90%;
      }
    }
    &__mainContent {
      display: flex;
      justify-content: space-between;
      height: 100%;
      @media (max-width: $xl) {
        flex-wrap: wrap;
      }
    }
    &__firstBlock {
      width: 30%;
      height: 90%;
      margin-top: 5%;

      @media (max-width: $xl) {
        width: 100%;
        height: auto;
      }
    }
    &__title {
      font-family: $bitter;
      font-size: 40px;
      line-height: 52px;
      color: $whiteText;
      font-weight: bold;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 100px);
      transition: 0.7s;
      @media (max-width: $sm-extra) {
        font-size: 32px;
        line-height: 28px;
      }
      @media (max-width: $sm-mini) {
        text-align: left;
        font-size: 18px;
        line-height: 26px;
      }
    }
    &__text {
      margin-top: 30px;
      font-family: $roboto;
      font-size: 23px;
      line-height: 36px;
      font-weight: 300;
      color: $whiteText;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 200px);
      transition: 1.5s;
      @media (max-width: $md) {
        margin-top: 15px;
        width: 85%;
        font-size: 21px;
      }
      @media (max-width: $sm-extra) {
        margin-top: 10px;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
      }
    }
    &__secondBlock {
      position: relative;
      width: 70%;
      background: url(../../assets/images/orbitImage.png) no-repeat;
      background-size: contain;
      background-position: center;
      @media (max-width: $xl) {
        height: 60%;
        margin: auto;
      }
      @media (max-width: $sm-mini) {
        margin: 0 auto;
        width: 100%;
        transform: rotate(145deg);
      }
      &__circle {
        position: absolute;
        @media (max-width: $sm-mini) {
          transform: rotate(-145deg);
        }
        &__title {
          font-family: $bitter;
          font-weight: bold;
          color: $blueText;
          font-size: 22px;
          line-height: 20px;
          width: 80%;
          height: 100%;
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: auto;
          @media (max-width: $xl) {
            font-size: 18px;
          }
          @media (max-width: $lg) {
            font-size: 16px;
          }
          @media (max-width: $md-extra) {
            font-size: 14px;
          }
        }
        &__border {
          position: absolute;
          // top: -5%;
          // left: -5%;
          // width: 107%;
          // height: 107%;
          // border-radius: 50%;
          // border-width: 2px;
          width: 110%;
          height: 110%;
          transform: translate(50px, 0px);
          border: 1px solid transparent;
          border-radius: 50%;
          transition: 2s;
          &:hover {
            // animation-play-state: running;
            transform: translate(-1px, -1px);
            width: 110%;
            height: 110%;
          }
          &__first {
            &:hover {
              border: 1px solid $firstCircleBg;
            }
            // border-style: dashed dashed solid solid;
            // border-color: $firstCircleBg;
            // -webkit-animation: rotating 8s linear forwards infinite;
            // -moz-animation: rotating 8s linear forwards infinite;
            // -ms-animation: rotating 8s linear forwards infinite;
            // -o-animation: rotating 8s linear forwards infinite;
            // animation: rotating 8s linear forwards infinite;
            // animation-play-state: paused;
          }
          &__second {
            &:hover {
              border: 1px solid $secondCircleBg;
            }
            // border-style: solid dashed solid dashed;
            // border-color: $secondCircleBg;
            // -webkit-animation: rotating 10s linear forwards infinite;
            // -moz-animation: rotating 10s linear forwards infinite;
            // -ms-animation: rotating 10s linear forwards infinite;
            // -o-animation: rotating 10s linear forwards infinite;
            // animation: rotating 10s linear forwards infinite;
            // animation-play-state: paused;
          }
          &__third {
            &:hover {
              border: 1px solid $thirdCircleBg;
            }
            // border-style: dashed solid dashed solid;
            // border-color: $thirdCircleBg;
            // -webkit-animation: rotating 15s linear forwards infinite;
            // -moz-animation: rotating 15s linear forwards infinite;
            // -ms-animation: rotating 15s linear forwards infinite;
            // -o-animation: rotating 15s linear forwards infinite;
            // animation: rotating 15s linear forwards infinite;
            // animation-play-state: paused;
          }
          &__fourth {
            &:hover {
              border: 1px solid $fourthCircleBg;
            }
            // border-style: solid dashed dashed dashed;
            // border-color: $fourthCircleBg;
            // -webkit-animation: rotating 5s linear forwards infinite;
            // -moz-animation: rotating 5s linear forwards infinite;
            // -ms-animation: rotating 5s linear forwards infinite;
            // -o-animation: rotating 5s linear forwards infinite;
            // animation: rotating 5s linear forwards infinite;
            // animation-play-state: paused;
          }
        }
        &__firstCircle {
          bottom: 15%;
          left: 10%;
          width: 9.5vw;
          height: 9.5vw;
          background: $firstCircleBg;
          border-radius: 50%;
          opacity: 0;
          visibility: hidden;
          transform: translate(0, 150px);
          transition: 2s;
          z-index: 4;
          @media (max-width: $md) {
            width: 15.5vw;
            height: 15.5vw;
            bottom: 10%;
            left: 5%;
          }
          @media (max-width: $sm) {
            width: 24vw;
            height: 24vw;
            left: -5%;
          }
          @media (max-width: $sm-mini) {
            width: 25vw;
            height: 25vw;
            left: 0;
            bottom: 20%;
          }
        }
        &__secondCircle {
          z-index: 3;

          bottom: 25%;
          left: 45%;
          width: 7.5vw;
          height: 7.5vw;
          background: $secondCircleBg;
          border-radius: 50%;
          opacity: 0;
          visibility: hidden;
          transform: translate(0, 150px);
          transition: 1s;
          @media (max-width: $md) {
            width: 12.5vw;
            height: 12.5vw;
          }
          @media (max-width: $sm) {
            width: 13.5vw;
            height: 13.5vw;
          }
          @media (max-width: $sm-mini) {
            width: 16vw;
            height: 16vw;
            bottom: 50%;
            left: 30%;
          }
        }
        &__thirdCircle {
          z-index: 2;

          top: 10%;
          left: 45%;
          width: 13vw;
          height: 13vw;
          background: $thirdCircleBg;
          border-radius: 50%;
          opacity: 0;
          visibility: hidden;
          transform: translate(0, 100px);
          transition: 0.6s;
          @media (max-width: $md) {
            width: 16vw;
            height: 16vw;
          }
          @media (max-width: $sm) {
            top: -5%;
            width: 26vw;
            height: 26vw;
            left: 40%;
          }
          @media (max-width: $sm-mini) {
            width: 27vw;
            height: 27vw;
            left: 60%;
            top: 50%;
          }
        }
        &__fourthCircle {
          z-index: 1;
          top: 40%;
          right: 5%;
          width: 9vw;
          height: 9vw;
          background: $fourthCircleBg;
          border-radius: 50%;
          opacity: 0;
          visibility: hidden;
          transform: translate(0, 200px);
          transition: 1s;
          @media (max-width: $md) {
            right: -5%;
            width: 13vw;
            height: 13vw;
          }
          @media (max-width: $sm) {
            width: 17vw;
            height: 17vw;
          }
          @media (max-width: $sm-mini) {
            width: 20vw;
            height: 20vw;
            right: 0%;
            top: 10%;
          }
        }

        &__hiddenCircle {
          position: absolute;
          opacity: 0;
          visibility: hidden;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-wrap: wrap;
          transition: 0.8s;
          background: white;
          border-radius: 50%;
          z-index: 3;

          &__wrap {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            text-align: center;
            width: 85%;
            @media (max-width: $md) {
              width: 80%;
            }
          }
          &__close {
            width: 20px;
            height: 20px;
            margin-top: 30px;
            @media (max-width: $md) {
              margin-top: 15px;
            }
            @media (max-width: $sm-mini) {
              margin-top: 10px;
              width: 13px;
              height: 13px;
            }
            & img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }
          &__firstCircle {
            top: -75%;
            left: -35%;
            height: 320px;
            width: 320px;
            transform: translate(-100px, 100px);
            @media (max-width: $md) {
              height: 280px;
              width: 280px;
            }
            @media (max-width: $sm-extra) {
              height: 220px;
              width: 220px;
            }
            @media (max-width: $sm-mini) {
              height: 186px;
              width: 186px;
            }
          }
          &__secondCircle {
            left: -85%;
            bottom: -100%;
            height: 400px;
            width: 400px;
            transform: translate(100px, 100px);
            @media (max-width: $md) {
              height: 330px;
              width: 330px;
              left: -135%;
            }
            @media (max-width: $sm-extra) {
              height: 255px;
              width: 255px;
            }
            @media (max-width: $sm-mini) {
              height: 230px;
              width: 230px;
              left: -125%;
              bottom: -100%;
            }
          }
          &__thirdCircle {
            left: -40%;
            top: -6%;
            height: 400px;
            width: 400px;
            transform: translate(-100px, -100px);
            @media (max-width: $md) {
              height: 330px;
              width: 330px;
            }
            @media (max-width: $sm-extra) {
              height: 275px;
              width: 275px;
              left: -80%;
            }
            @media (max-width: $sm-mini) {
              height: 245px;
              width: 245px;
              top: -120%;
              left: -30%;
            }
          }
          &__fourthCircle {
            left: -50%;
            top: -20%;
            height: 370px;
            width: 370px;
            transform: translate(100px, -100px);
            @media (max-width: $md) {
              height: 330px;
              width: 330px;
              left: -160%;
              top: -50%;
            }
            @media (max-width: $sm-extra) {
              height: 265px;
              width: 265px;
              left: -240%;
              top: -125%;
            }
            @media (max-width: $sm-mini) {
              height: 230px;
              width: 230px;
              top: -140%;
              left: -15%;
            }
          }
          &__title {
            font-family: $bitter;
            font-size: 22px;
            font-weight: bold;
            line-height: 20px;
            color: $darkestGrayText;
            width: 100%;
            margin-bottom: 35px;
            @media (max-width: $md) {
              margin-bottom: 10px;
              font-size: 18px;
            }
            @media (max-width: $sm-extra) {
              font-size: 16px;
              line-height: 23px;
              margin-bottom: 5px;
            }
            @media (max-width: $sm-mini) {
              font-size: 14px;
              line-height: 20px;
              margin-bottom: 5px;
            }
          }
          &__text {
            font-family: $roboto;
            font-weight: 100;
            font-size: 17px;
            line-height: 25px;
            color: $darkestGrayText;
            @media (max-width: $md) {
              font-size: 14px;
            }
            @media (max-width: $sm-extra) {
              font-size: 13px;
              line-height: 17px;
            }
            @media (max-width: $sm-mini) {
              font-size: 12px;
              line-height: 16px;
            }
          }
        }
      }
    }
  }

  &__getStarted {
    width: 100%;
    margin-top: 85px;
    margin-bottom: 85px;
    @media (max-width: $sm-mini) {
      margin-bottom: 20px;
      margin-top: 30px;
    }
    &__wrap {
      width: 70%;
      margin: auto;
      @media (max-width: $sm-extra) {
        width: 85%;
        margin: auto;
      }
    }
    &__title {
      font-family: $bitter;
      font-size: 40px;
      font-weight: bold;
      color: $darkGrayText;
      text-align: center;
      @media (max-width: $sm-mini) {
        line-height: 26px;
        font-size: 18px;
      }
    }
    &__list {
      margin-top: 50px;
      display: flex;
      justify-content: space-between;
      @media (max-width: $lg) {
        flex-wrap: wrap;
      }
      @media (max-width: $sm-mini) {
        margin-top: 20px;
      }
      &__single {
        width: 23%;
        font-family: $roboto;
        opacity: 0;
        overflow: hidden;
        transform: translate(0, 100px);
        @media (max-width: $lg) {
          width: 47%;
          margin: 2% 0;
        }
        @media (max-width: $md) {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          align-items: flex-start;
        }
        @media (max-width: $sm-mini) {
          align-items: flex-start;
        }
        @media (max-width: $sm-mini) {
          margin: 0;
        }

        &__wrap {
          @media (max-width: $md) {
            width: 80%;
            margin-left: 5%;
          }
          @media (max-width: $sm-extra) {
            width: 75%;
          }
        }

        &__id {
          font-size: 34px;
          font-weight: 900;
          color: $blueText;
          line-height: 25px;
          width: 60px;
          height: 60px;
          background: $mainPageGetStartedNumbersBG;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          @media (max-width: $sm-mini) {
            width: 33px;
            height: 33px;
            font-size: 18px;
            line-height: 18px;
          }
        }
        &__title {
          margin-top: 30px;
          font-size: 18px;
          font-weight: 500;
          color: $headerSummText;
          @media (max-width: $md) {
            margin-top: 5px;
          }
          @media (max-width: $sm-mini) {
            font-size: 14px;
            line-height: 20px;
          }
        }
        &__text {
          margin-top: 25px;
          font-size: 16px;
          font-weight: 300;
          color: $mainPageGetStartedText;
          line-height: 25px;
          @media (max-width: $md) {
            margin-top: 10px;
          }
          @media (max-width: $sm-extra) {
            top: -20px;
            font-size: 12px;
            line-height: 16px;
          }
        }
      }
    }
  }

  &__startParthnership {
    width: 100%;
    background: url(../../assets/images/beASponsorLastBlockBG.png) no-repeat;
    background-position: center;
    height: 345px;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $sm-mini) {
      height: 184px;
      background-position: center;
    }
    &__wrap {
      width: 55%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      @media (max-width: $md-extra) {
        width: 80%;
      }
    }
    &__title {
      font-family: $bitter;
      font-weight: bold;
      font-size: 34px;
      line-height: 48px;
      text-align: center;
      color: $darkGrayText;
      width: 100%;
      margin-bottom: 55px;
      @media (max-width: $xl) {
        font-size: 28px;
        line-height: 35px;
      }
      @media (max-width: $md-extra) {
        margin-bottom: 25px;
      }
      @media (max-width: $sm-extra) {
        font-size: 24px;
        line-height: 32px;
      }
      @media (max-width: $sm-mini) {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 10px;
      }
    }
  }
}

.show {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.showPauseBtn {
  &:hover > button {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
