@import '../../../@theme/index';

.nav {
  font-family: $helvetica;
  font-weight: 400;
  height: 100%;
  @media (max-width: $xl) {
    display: flex;
  }
  &__list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 18px;
    height: 100%;
    &.hide {
      display: none;
    }

    @media (max-width: $xl) {
      display: none;
      font-size: 16px;
    }
    @media (max-width: $lg) {
      font-size: 13px;
    }
    @media (max-width: $md-extra) {
      font-size: 12px;
    }
  }
  &__link {
    margin: auto;
    text-align: center;
    position: relative;
    border: 1px solid transparent;
    height: 100%;
    box-sizing: border-box;

    & a {
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      color: $navLinksText;
      position: relative;
      height: 100%;
      transition: 0.3s;
      width: 115px;
      margin: 0 15px;
      @media (max-width: $xxl) {
        margin: 0 5px;
      }
      &::after {
        content: '';
        width: 0%;
        margin: auto;
        position: absolute;
        height: 2px;
        background: transparent;
        top: 100%;
        left: 0;
        transition: 0.5s;
      }
      &:hover {
        color: $blueText;
        &::after {
          content: '';
          width: 100%;
          margin: auto;
          position: absolute;
          height: 2px;
          top: 100%;
          background: #4285f4;
          left: 0;
          bottom: 0;
        }
      }
      &.active {
        color: $blueText;
        &::after {
          content: '';
          width: 100%;
          margin: auto;
          position: absolute;
          height: 2px;
          background: #4285f4;
          left: 0;
          bottom: 0;
        }
      }
    }
  }
  &__menu-arrow-down {
    position: absolute;
    top: 50%;
    right: -15%;
    width: 12px;
    height: auto;
    transform: translate(-50%, -50%);
    @media (max-width: $xl) {
      right: -20%;
      top: 40%;
    }
    @media (max-width: $xl) {
      width: 5px;
      right: -10%;
      top: 30%;
    }
  }
  &__submenu {
    position: absolute;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    width: 100%;
  }
  &__mobile {
    display: none;
    &.show {
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    @media (max-width: $xl) {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-closeMenu {
      width: 25px;
      height: 25px;
      @media (max-width: $sm-extra) {
        width: 19px;
        height: 19px;
      }
      background-position: center;
      background-size: contain;
      position: absolute;
      top: 5%;
      left: 10%;
      cursor: pointer;
    }
    &-imageWrap {
      display: flex;
      justify-content: center;
      align-items: center;
      & img {
        object-fit: contain;
        width: 30px;
        height: 30px;
        margin: auto;
        @media (max-width: $lg) {
          width: 25px;
          height: 25px;
        }
        @media (max-width: $md) {
          width: 20px;
          height: 20px;
        }
        @media (max-width: $sm-extra) {
          width: 28px;
          height: 19px;
        }
      }
    }
    &-menu {
      position: absolute;
      width: 60vw;
      height: 100vh;
      left: 0;
      right: 0;
      top: 0;
      background: #ffffff;
      display: flex;
      justify-content: center;
      align-items: center;
      -webkit-box-shadow: 8px 2px 13px -6px rgba(34, 60, 80, 0.38);
      -moz-box-shadow: 8px 2px 13px -6px rgba(34, 60, 80, 0.38);
      box-shadow: 8px 2px 13px -6px rgba(34, 60, 80, 0.38);
      transition: 0.4s;
      &.show {
        opacity: 1;
        visibility: visible;
        transform: translate(0px);
      }
      &.hide {
        opacity: 0;
        visibility: hidden;
        transform: translate(-200px);
      }
      &.myAccount {
        left: -22.5%;
        width: 35vw;
      }
      &-wrapper {
        width: 85%;
        position: absolute;
        top: 10%;
        left: 10%;
      }
      &-link {
        color: #333333;
        text-decoration: none;
        font-family: $roboto;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        min-height: 70px;
        position: relative;
        display: flex;
        align-items: center;
        width: 90%;
        display: flex;
        align-items: center;
        margin: auto;
        padding: 0 5%;
        @media (max-width: $md-extra) {
          font-size: 18px;
          min-height: 44px;
        }
        & img {
          transition: 0.3s;
          position: absolute;
          transform: translate(-50%, -50%) rotate(-0deg);
          top: 50%;
          right: 0%;
          width: 14px;
          &.imageRotate {
            transform: translate(-50%, -50%) rotate(-180deg);
          }
        }
      }
      &-mainMenu {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: 80%;
        flex-wrap: nowrap;
        flex-direction: column;
        margin-top: 10%;
        overflow-x: auto;
        & > li {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          width: 100%;
          transition: 0.5s;
          flex-wrap: wrap;
        }
      }
      &-submenu {
        display: none;
        transition: 0.5s;
        @media (max-width: $sm-extra) {
          margin-top: 10px;
        }
        &.show {
          display: block;
        }
        & > li {
          font-style: normal;
          font-weight: normal;

          &:first-child {
            margin: 0;
          }
          & > a {
            text-decoration: none;
            color: $userMenuFullNameText;
            display: block;
            font-size: 18px;
            width: 90%;
            padding: 10px 0 5px 10%;
            @media (max-width: $md-extra) {
              padding: 5px 0 5px 10%;
              margin-bottom: 5px;
              font-size: 14px;
            }
            & > span {
              font-style: normal;
              font-weight: normal;
              color: $submenuLinksText;
              display: flex;
              width: 90%;
              font-size: 16px;
              line-height: 20px;

              @media (max-width: $md-extra) {
                line-height: 16px;
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}
