@import '../../../../@theme/index';

.editPassword {
  width: 100%;
  &__fields {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: $lg) {
      flex-wrap: wrap;
    }
  }
  &__label {
    position: relative;
    display: flex;
    align-items: center;
    font-family: $roboto;
    font-size: 16px;
    font-weight: 500;
    color: $darkestGrayText;
    margin-bottom: 10px;
    &__required {
      color: $requiredFormFieldText;
      margin: 0 5px;
    }
    &__optional {
      color: $grayText;
    }
    &__warningIcon {
      display: inline-block;
    }
  }
  &__inputField {
    width: 31%;
    @media (max-width: $lg) {
      width: 100%;
      margin-bottom: 30px;
    }
    &__input {
      position: relative;
      &__hint {
        position: absolute;
        &.hidePassword {
          position: absolute;
          background: url(../../../../assets/images/ui-icons/passwordGrayEye.png)
            no-repeat;
          background-size: contain;
          background-position: center;
          transform: translate(-50%, -50%);
          top: 50%;
          right: 2%;
          width: 25px;
          height: 25px;
          @media (max-width: $sm-extra) {
            width: 20px;
            height: 20px;
          }
        }
        &.showPassword {
          position: absolute;
          background: url(../../../../assets/images/ui-icons/passwordBlackEye.png)
            no-repeat;
          background-size: contain;
          background-position: center;
          transform: translate(-50%, -50%);
          top: 50%;
          right: 2%;
          width: 25px;
          height: 25px;
          @media (max-width: $sm-extra) {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
    & input {
      width: 100%;
      height: 40px;
      padding: 15px 20px;
      box-sizing: border-box;
      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      color: $blackText;
      border: 1px solid $btnLightGrayBg;
      border-radius: 4px;
      &:focus {
        outline: none;
        border: 1px solid $blueText;
      }
      &[type='password'] {
        letter-spacing: 3px;
      }
      &.error {
        border: 1px solid $inputErrorBorder;
      }
      &.success {
        border: 1px solid $inputSuccessBorder;
      }
    }

    &__errorMessage {
      position: absolute;
      box-sizing: border-box;
      padding: 10px 20px;
      top: 100%;
      width: 100%;
      font-family: $roboto;
      font-size: 14px;
      font-weight: 400;
      color: $darkGrayText;
      background: $myAccountChangePassErrorBg;
      border: 1px solid #ff7c7c;
      border-radius: 2px;
      opacity: 0;
      visibility: hidden;
      transition: 0.5s;
      &.show {
        opacity: 1;
        visibility: visible;
      }
      &::before {
        content: '';
        position: absolute;
        width: 3px;
        top: 0;
        left: 0;
        height: 100%;
        background: $changePasswordErrorBorder;
      }
    }
  }
  &__note {
    margin-top: 30px;
    width: 90%;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: $grayText;
  }
  &__boldText {
    font-family: $roboto;
    font-weight: 500;
    color: $cancelBtnText;
  }
  &__link {
    font-family: $roboto;
    text-decoration: none;
    color: $blueText;
  }

  &__forgotPassword {
    margin-top: 15px;
    @media (max-width: $lg) {
      margin: 0;
    }
  }
  &__buttons {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    @media (max-width: $lg) {
      margin-top: 30px;
    }
    &__btn {
      font-family: $roboto;
      font-size: 14px;
      font-weight: 500;
      border: 1px solid transparent;
      height: 30px;
      box-sizing: border-box;
      &:focus {
        border: 1px solid $blueText;
      }
      &:disabled {
        background: $btnDisabledBg;
        color: $mainPageCategories;
      }
      &__cancel {
        color: $darkGrayText;
        background: $btnLightGrayBg;
        border: 1px solid $borderSignOutBtn;
        margin-right: 20px;
        width: 90px;
      }
      &__save {
        color: $whiteText;
        background: $btnBlueBg;
        width: 125px;
        &:focus {
          border: 1px solid $blackText;
        }
      }
    }
    &__cancel {
      margin-right: 20px;
    }
  }
}
