@import '../../@theme/index';

@keyframes firstCloudRoad {
  0% {
    left: -5%;
  }
  100% {
    left: 105%;
  }
}

@keyframes secondCloudRoad {
  0% {
    left: 100%;
  }
  50% {
    left: 55%;
  }
  100% {
    left: -5%;
  }
}

@keyframes airBaloonRoad {
  0% {
    top: 5%;
  }
  50% {
    top: 10%;
  }
  100% {
    top: 5%;
  }
}

@keyframes cloudNearBallon {
  0% {
    left: 35%;
  }
  50% {
    left: 45%;
  }
  100% {
    left: 35%;
  }
}

.roadmap {
  width: 100%;
  position: relative;
  overflow: hidden;
  &__mainImage {
    max-width: 100%;
  }
  &__clouds {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    &__singleCloud {
      position: absolute;
      &.firstCloud {
        top: 15%;
        animation: firstCloudRoad linear 70s infinite;
        & img {
          max-width: 65px;
          min-width: 25px;
          object-fit: contain;
        }
      }
      &.secondCloud {
        top: 20%;
        animation: secondCloudRoad linear 65s infinite alternate;

        & img {
          max-width: 45px;
          min-width: 15px;
          object-fit: contain;
        }
      }

      &.airBaloon {
        animation: airBaloonRoad 10s infinite linear;
        left: 50%;
        & img {
          max-width: 85px;
          min-width: 45px;
          object-fit: contain;
        }
      }
      &.nearBallonCloud {
        left: 45%;
        top: 10%;
        animation: cloudNearBallon 30s infinite linear;
      }
    }
  }
}
